import React, { Component } from "react";
import "./AddPayment.scss";
import { Form, Button, Modal } from "semantic-ui-react";
import { Field, reduxForm, /*reset*/ } from "redux-form";
import { required,/* email*/ } from "redux-form-validators";
import { connect } from "react-redux";
import { FormField, SelectField } from "../../../Components/Common/index";
import {
  getUserPaymentMethods,
  openCloseEditModal
} from "../../../redux/reducers/currency";

export class EditPayment extends Component {
  state = { open: false };

  close = () => {
    this.props.openCloseEditModal(false);
  };
  componentDidMount = () => {
    this.initialiseEditForm();
  };
  initialiseEditForm = () => {
    let { initialize } = this.props;
    initialize({
      payment_type: this.props.editPaymentData.payment_type,
      currency_id: this.props.editPaymentData.currency_id,
      payment_identifier: this.props.editPaymentData.payment_identifier,
      detailed_info: this.props.editPaymentData.detailed_info
    });
  };

  sumbitEditForm = () => {
    alert("edit");
  };

  getPaymentMethods = () => {
    this.props
      .getUserPaymentMethods()
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {});
  };
  render() {
    let { handleSubmit } = this.props;
    return (
      <div>
        <Modal
          size={"small"}
          closeIcon
          open={this.props.openEditPayment}
          onClose={this.close}
          closeOnDimmerClick={false}
          className={"EditModal"}
        >
          <Modal.Header>Update Payemnt</Modal.Header>
          <Modal.Content>
            <div className="AddPaymentInfo">
              <Form
                className="informationSec"
                onSubmit={handleSubmit(this.sumbitEditForm)}
              >
                <Form.Group>
                  <Form.Field>
                    <label>Payment method</label>
                    <Field
                      type="text"
                      name="payment_type"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Payment method"
                    ></Field>
                  </Form.Field>
                  <Form.Field>
                    <label>Currency</label>

                    <Field
                      name="currency_id"
                      validate={[required()]}
                      component={SelectField}
                      selectOnBlur={false}
                      defaultValue={this.props.editPaymentData.currency_id}
                      children={this.props.fiatCoins}
                      placeholder="Select your currency"
                    ></Field>
                  </Form.Field>
                </Form.Group>
                <Form.Group>
                  <Form.Field>
                    <label>Identifier</label>
                    <Field
                      type="text"
                      name="payment_identifier"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Identifier"
                    ></Field>
                  </Form.Field>
                  <Form.Field>
                    <label>Description</label>
                    <Field
                      type="text"
                      name="detailed_info"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Description"
                    ></Field>
                  </Form.Field>
                </Form.Group>
                <Form.Field className="addBtn">
                  <Button type="submit">Update Payement</Button>
                </Form.Field>
              </Form>
            </div>
          </Modal.Content>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    fiatCoins: state.currency.fiatCoins,
    cryptoCoins: state.currency.cryptoCoins,
    paymentMethods: state.currency.paymentMethods,
    openEditPayment: state.currency.openEditPayment,
    editPaymentData: state.currency.editPaymentData,
    EditPaymentForm: state.form.EditPaymentForm
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getUserPaymentMethods: () => dispatch(getUserPaymentMethods()),
    openCloseEditModal: status => dispatch(openCloseEditModal(status))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "EditPaymentForm" })(EditPayment));
