import { UserService } from "../../services/UserService";
import { startStopLoading } from "./loading";
import { toast } from "../../Components/Toast/Toast";

import { LOGIN_URL } from "../../constant";
const LOGIN = "auth/LOGIN";
const SAVE_USER_INFO = "SAVE_USER_INFO";

export const USER_STATUS_UPDATE = "/USER_STATUS_UPDATE";
const initialState = {
  jwtToken: undefined,
  userProfile: {},
};

export default function user(state = initialState, action = {}) {
  switch (action.type) {
    case LOGIN:
      return { ...state };
    case SAVE_USER_INFO:
      return {
        ...state,
        userProfile: action.result,
      };

    default:
      return state;
  }
}
export const login = (user) => {
  console.log("LOGIN DATA", user);
  return (dispatch, getState) => {
    dispatch(startStopLoading(true));

    return new Promise((resolve, reject) => {
      UserService.login(user)
        .then((res) => {
          dispatch(startStopLoading(false));

          resolve(res);
        })
        .catch((ex) => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};

export function checkAuthToken(data) {
  return (dispatch) => {
    if (data.token) {
      return UserService.checkAuthTokens(data).then((success) => {
        if (!success.data.data) {
          toast.error("Session expired");
          localStorage.removeItem("JwtToken");
          //   window.open("http://10.1.0.71:4200/", "_self");
          window.open(`${LOGIN_URL}`, "_self");
        }
      });
    }
  };
}
export const getKyc = () => {
  return (dispatch, getState) => {
    dispatch(startStopLoading(true));

    return new Promise((resolve, reject) => {
      UserService.getKyc({ jwt: localStorage.getItem("JwtToken") })
        .then((res) => {
          dispatch(startStopLoading(false));
          dispatch({ type: SAVE_USER_INFO, result: res.data.data });

          resolve(res);
        })
        .catch((ex) => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};
