import React from "react";
import { Input, Select } from "semantic-ui-react";
import "./FormField.scss";
const InputSelectWrap = props => {
  const { meta = {} } = props;
  console.log("props", props);
  const inputSelectWrapProps = {
    type: "text",
    name: props.input.name,
    value: props.input.value,
    id: props.input.name,
    onChange: props.input.onChange,
    placeholder: "Amount"
  };
  return (
    <React.Fragment>
      <Input action {...inputSelectWrapProps}>
        <input />
        <Select compact options={props.children} defaultValue="INR" />
      </Input>
      {meta.touched && meta.error ? (
        <div className="form__field-error">{meta.error}</div>
      ) : null}
    </React.Fragment>
  );
};

export { InputSelectWrap };
