import React, { PureComponent } from "react";
import {
  Container,
  Tab,
  Dropdown,
  Table,
  Breadcrumb,
  Pagination,
  Confirm,
  List,
  Modal
} from "semantic-ui-react";
import "./MyBids.scss";
import PageTitle from "../../../Components/PageTitle/PageTitle";

import { connect } from "react-redux";
import {
  getBuySellOfferById,
  buySellBids,
  emptyOfferViewed,
  approvesentSell,
  getUserDetails,
  showUserDetails
} from "../../../redux/reducers/offer";

import { smallestunitFormat, listDateFormat } from "../../../Utils/helper";
import UserDetails from "../UserDetails/UserDetails";
const filterOptions = [
  {
    key: "1",
    text: "Pending",
    value: "0"
  },
  {
    key: "2",
    text: "Approved",
    value: "1"
  },
  {
    key: "3",
    text: "Disapproved",
    value: "2"
  },

  {
    key: "4",
    text: "Cancelled",
    value: "3"
  },
  {
    key: "5",
    text: "Claimed",
    value: "4"
  }
];

const BuySellHistory = ({
  type,
  record,
  viewMyOffer,
  approvePayment,
  viewUser
}) => {
  console.log("data", record);

  return (
    <Tab.Pane attached={false}>
      <Table inverted selectable>
        <Table.Header>
          <Table.Row>
            {type === "SELL" && <Table.HeaderCell>Txn Id</Table.HeaderCell>}
            <Table.HeaderCell>
              {type === "SELL" ? "Recieved Amount" : "Paid Amount"}
            </Table.HeaderCell>
            <Table.HeaderCell>Quantity</Table.HeaderCell>
            <Table.HeaderCell>Created At</Table.HeaderCell>
            <Table.HeaderCell>Status</Table.HeaderCell>
            <Table.HeaderCell textAlign="center">Actions</Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {record.map((data, index) => (
            <Table.Row key={index}>
              {type === "SELL" && (
                <Table.Cell>
                  <a href>{data.t_id}</a>{" "}
                </Table.Cell>
              )}
              <Table.Cell>
                {smallestunitFormat(data.payment_amount)}{" "}
                {data.fiat_coin.toUpperCase()}
              </Table.Cell>
              <Table.Cell>
                <a href>
                  {smallestunitFormat(data.quantity)}{" "}
                  {data.crypto_coin.toUpperCase()}{" "}
                </a>{" "}
              </Table.Cell>
              <Table.Cell>{listDateFormat(data.created_at, 1)}</Table.Cell>
              <Table.Cell>
                {data.status === 0
                  ? "Pending"
                  : data.status === 1
                  ? "Approved"
                  : data.status === 2
                  ? "Disapproved"
                  : data.status === 3
                  ? "Cancelled"
                  : "Claimed"}
              </Table.Cell>

              <Table.Cell textAlign="center">
                <a href className="btn" onClick={() => viewMyOffer(data.o_id)}>
                  View offer
                </a>
                <a href className="btn" onClick={() => viewUser(data.biduser)}>
                  View user
                </a>
              </Table.Cell>
              {type === "SELL" && (data.t_id !== 0 && data.status === 0) && (
                <Table.Cell textAlign="center">
                  <a href
                    className="btn"
                    onClick={() => approvePayment(data.bid_id)}
                  >
                    Approve
                  </a>
                </Table.Cell>
              )}
            </Table.Row>
          ))}
          {record.length === 0 && (
            <Table.Row>
              <Table.Cell
                style={{ textAlign: "center" }}
                Colspan={type === "BUY" ? 6 : 7}
              >
                No records found
              </Table.Cell>
            </Table.Row>
          )}
        </Table.Body>
      </Table>
    </Tab.Pane>
  );
};

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "My Sent Bids", active: true }
];

class MyBids extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      defaultTab: 0,
      defaultTabList: 0,
      listType: 0,
      buySellPage: 1,
      open: false,
      status: 0,
      defaultFilter: "0",
      viewModal: false,
      approveConfirm: false,
      approveBidId: ""
    };
  }
  componentDidMount = () => {
    // get buysell list
    this.getBuySellBidList();
  };

  viewUser = userId => {
    let data = {
      user_id: userId
    };
    this.props
      .getUserDetails(data)
      .then(res => {
        this.props.showUserDetails(true);
      })
      .catch(error => {});
  };
  // approve sell bid
  approveConfirm = () => {
    let data = {
      bid_id: this.state.approveBidId
    };
    // call api
    this.props
      .approvesentSell(data)
      .then(res => {
        this.setState({ approveConfirm: false, approveBidId: "" });
        this.getBuySellBidList();
      })
      .catch(error => {});
  };
  //   close confirm modal
  closeApproveConfirm = () => {
    this.setState({ approveConfirm: false, approveBidId: "" });
  };

  approvePayment = id => {
    this.setState({ approveConfirm: true, approveBidId: id });
  };
  viewMyOffer = id => {
    let data = {
      type: this.state.listType === 1 ? 0 : 1,
      page: 1,
      pagination: 1,
      offer_id: id,
      single: 1
    };
    this.props
      .getBuySellOfferById(data)
      .then(res => {
        this.setState({ viewModal: true });
      })
      .catch(res => {});
  };

  //   get all buy sell bid list
  getBuySellBidList = () => {
    let data = {
      type: this.state.listType,
      page: this.state.buySellPage,
      pagination: 1,
      status: this.state.status
    };
    this.props
      .buySellBids(data)
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {
        console.log(error);
      });
  };

  listTabChange = (e, data) => {
    this.setState(
      {
        defaultTab: data.activeIndex,
        defaultTabList: data.activeIndex,
        listType: data.activeIndex,
        buySellPage: 1,
        defaultFilter: "0"
      },
      () => {
        // getBuySelbid
        this.getBuySellBidList();
      }
    );
  };
  onClose = () => {
    this.props.emptyOfferViewed();
    this.setState({ viewModal: false });
  };
  pageChange = (e, data) => {
    console.log(data.activePage);
    this.setState({ buySellPage: data.activePage }, () => {
      this.getBuySellBidList();
    });
  };
  onFilter = (e, data) => {
    console.log(data.value);
    this.setState({ status: data.value }, () => {
      this.getBuySellBidList();
    });
  };
  render() {
    let { offerDetailById } = this.props;
    console.log("hh", offerDetailById);
    //listing buysell
    const historyBuySell = [
      {
        menuItem: " Your buy bids on sell offer ",
        render: () => (
          <BuySellHistory
            type="BUY"
            record={this.props.myBids}
            viewMyOffer={this.viewMyOffer}
            approvePayment={this.approvePayment}
            viewUser={this.viewUser}
          />
        )
      },
      {
        menuItem: "Your sell bids on buy offer ",
        render: () => (
          <BuySellHistory
            type="SELL"
            record={this.props.myBids}
            viewMyOffer={this.viewMyOffer}
            approvePayment={this.approvePayment}
            viewUser={this.viewUser}
          />
        )
      }
    ];

    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="My Sent Bids" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>
        <Container className="MainContainerBid">
          <Dropdown
            placeholder="Filter"
            selectOnBlur={false}
            selection
            defaultValue={this.state.defaultFilter}
            options={filterOptions}
            onChange={(e, data) => this.onFilter(e, data)}
          />
          <div className="tabsBox tabsBoxSec">
            <Tab
              menu={{ secondary: true }}
              panes={historyBuySell}
              activeIndex={this.state.defaultTabList}
              onTabChange={(e, data) => this.listTabChange(e, data)}
            />
          </div>
          {this.props.totalBids > 0 && (
            <div className="pageBottom">
              <Pagination
                boundaryRange={0}
                activePage={this.state.buySellPage}
                ellipsisItem={null}
                firstItem={null}
                lastItem={null}
                siblingRange={1}
                totalPages={Math.ceil(this.props.totalBids / 10)}
                onPageChange={(e, data) => this.pageChange(e, data)}
              />
            </div>
          )}
        </Container>
        {offerDetailById.length > 0 && (
          <Modal
            className="bidConfirm"
            open={this.state.viewModal}
            onClose={this.onClose}
            centered={false}
            closeIcon
          >
            <Modal.Header>Offer Details</Modal.Header>
            <Modal.Content image>
              <List>
                <List.Item>
                  Seller : <span> {offerDetailById[0].email}</span>
                </List.Item>
                <List.Item>
                  Payment method :{" "}
                  <span>{offerDetailById[0].payment_type}</span>
                </List.Item>

                <List.Item>
                  Offer Price :{" "}
                  <span>
                    {smallestunitFormat(offerDetailById[0].bidprice)}{" "}
                    {offerDetailById[0].fiat_coin.toUpperCase()}/
                    {offerDetailById[0].crypto_coin.toUpperCase()}
                  </span>
                </List.Item>

                <List.Item>
                  Date :{" "}
                  <span>{listDateFormat(offerDetailById[0].created_at)}</span>
                </List.Item>
              </List>
            </Modal.Content>
          </Modal>
        )}

        {/* //bidConfirm model */}
        <Confirm
          className="asdf"
          open={this.state.approveConfirm}
          onCancel={this.closeApproveConfirm}
          onConfirm={this.approveConfirm}
        />

        {/* user detail compo */}

        <UserDetails></UserDetails>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    myBids: state.offer.myBids,
    totalBids: state.offer.totalBids,
    offerDetailById: state.offer.offerDetailById
  };
};
const mapDispatchToProps = dispatch => {
  return {
    buySellBids: data => dispatch(buySellBids(data)),
    getBuySellOfferById: data => dispatch(getBuySellOfferById(data)),
    emptyOfferViewed: () => dispatch(emptyOfferViewed()),
    approvesentSell: data => dispatch(approvesentSell(data)),
    showUserDetails: status => dispatch(showUserDetails(status)),

    getUserDetails: data => dispatch(getUserDetails(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyBids);
