import React, { PureComponent } from "react";
import {
  Container,
  Tab,
  Dropdown,
  Table,
  Breadcrumb,
  Pagination,
  Button,
  List,
  Modal,
  Confirm,
  Form
} from "semantic-ui-react";
import "./MyRecievedBids.scss";
import PageTitle from "../../../Components/PageTitle/PageTitle";

import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { required, email } from "redux-form-validators";
import {
  getBuySellOfferById,
  buySellBidsRecieved,
  emptyOfferViewed,
  approveBuyBid,
  disapproveBuyBid,
  sendMoney,
  showUserDetails,
  getUserDetails
} from "../../../redux/reducers/offer";
import {
  FormField,
  SelectField,
  TextAreas
} from "../../../Components/Common/index";
import UserDetails from "../UserDetails/UserDetails";

import { smallestunitFormat, listDateFormat } from "../../../Utils/helper";
const filterOptions = [
  {
    key: "1",
    text: "Pending",
    value: "0"
  },
  {
    key: "2",
    text: "Approved",
    value: "1"
  },
  {
    key: "3",
    text: "Disapproved",
    value: "2"
  },

  {
    key: "4",
    text: "Cancelled",
    value: "3"
  },
  {
    key: "5",
    text: "Claimed",
    value: "4"
  }
];

const BuySellHistory = ({
  type,
  record,
  viewMyOffer,
  approveBuyBid,
  disapproveBuyBid,
  viewUser,
  sendMoney
}) => {
  console.log("data", record);

  return (
    <Tab.Pane attached={false}>
      <Table inverted selectable>
        <Table.Header>
          <Table.Row>
            {type === "BUY" && <Table.HeaderCell>Txn Id</Table.HeaderCell>}
            <Table.HeaderCell>
              {type === "BUY" ? "Recieved Amount" : "Paid Amount"}
            </Table.HeaderCell>
            <Table.HeaderCell>Quantity</Table.HeaderCell>
            <Table.HeaderCell>Created At</Table.HeaderCell>
            <Table.HeaderCell>Status</Table.HeaderCell>

            <Table.HeaderCell textAlign="center">Action</Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {record.map((data, index) => (
            <Table.Row key={index}>
              {type === "BUY" && (
                <Table.Cell>
                  <a href>{data.t_id}</a>{" "}
                </Table.Cell>
              )}
              <Table.Cell>
                {smallestunitFormat(data.payment_amount)}{" "}
                {data.fiat_coin.toUpperCase()}
              </Table.Cell>
              <Table.Cell>
                <a href>
                  {smallestunitFormat(data.quantity)}{" "}
                  {data.crypto_coin.toUpperCase()}{" "}
                </a>{" "}
              </Table.Cell>
              <Table.Cell>{listDateFormat(data.created_at, 1)}</Table.Cell>
              <Table.Cell>
                {data.status === 0
                  ? "Pending"
                  : data.status === 1
                  ? "Approved"
                  : data.status === 2
                  ? "Disapproved"
                  : data.status === 3
                  ? "Cancelled"
                  : "Claimed"}
              </Table.Cell>
              <Table.Cell textAlign="right">
                <a href className="btn" onClick={() => viewMyOffer(data.o_id)}>
                  View offer
                </a>
                <a href className="btn" onClick={() => viewUser(data.biduser)}>
                  View user
                </a>
                {type === "BUY" && (
                  <a href className="btn" onClick={() => approveBuyBid(data.bid_id)}>
                    Approve
                  </a>
                )}
                {(data.t_id === undefined ||
                  data.t_id === null ||
                  data.t_id === "" ||
                  data.t_id === 0) && (
                  <a href
                    className="btn"
                    onClick={() => disapproveBuyBid(data.bid_id)}
                  >
                    Disapprove
                  </a>
                )}
                {type === "SELL" &&
                  (data.t_id === undefined ||
                    data.t_id === null ||
                    data.t_id === "" ||
                    data.t_id === 0) && (
                    <a href
                      className="btn"
                      onClick={() => sendMoney(data.bid_id, data)}
                    >
                      Send Money
                    </a>
                  )}
              </Table.Cell>
            </Table.Row>
          ))}
          {record.length === 0 && (
            <Table.Row>
              <Table.Cell
                style={{ textAlign: "center" }}
                Colspan={type === "BUY" ? 8 : 6}
              >
                No records found
              </Table.Cell>
            </Table.Row>
          )}
        </Table.Body>
      </Table>
    </Tab.Pane>
  );
};

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "My Recieved Bids", active: true }
];

class MyrecievedBids extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      defaultTab: 0,
      defaultTabList: 0,
      listType: 0,
      buySellPage: 1,
      open: false,
      status: 0,
      defaultFilter: "0",
      viewModal: false,
      approvedBidId: "",
      disapprovedBidId: "",
      approveConfirm: false,
      disapproveConfirm: false,
      sendMoneyConfirm: false,
      sendMoneyBid_id: "",
      sendMoneyDetails: {}
    };
  }
  closeSendMoneyModal = () => {
    this.setState({ sendMoneyConfirm: false, sendMoneyBid_id: "" });
  };
  sendMoney = (id, data) => {
    this.setState({
      sendMoneyConfirm: true,
      sendMoneyBid_id: id,
      sendMoneyDetails: data
    });
  };
  sendMoneySubmit = values => {
    let data = {
      bid_id: this.state.sendMoneyBid_id,
      t_id: values["t_id"]
    };

    this.props
      .sendMoney(data)
      .then(res => {
        this.setState({ sendMoneyConfirm: false, sendMoneyBid_id: "" });
        this.getBuySellRecievedBidList();
      })
      .catch(eror => {});
  };

  closeApproveConfirm = () => {
    this.setState({ approvedBidId: "", approveConfirm: false });
  };
  approveConfirm = () => {
    let data = {
      bid_id: this.state.approvedBidId
    };
    this.props
      .approveBuyBid(data)
      .then(res => {
        this.setState({ approvedBidId: "", approveConfirm: false });
        this.getBuySellRecievedBidList();
      })
      .catch(error => {});
  };
  approveBuyBid = id => {
    this.setState({ approvedBidId: id, approveConfirm: true });
  };
  disapproveBuyBid = id => {
    this.setState({ disapprovedBidId: id, disapproveConfirm: true });
  };
  closeDisapproveModel = () => {
    this.setState({ disapprovedBidId: "", disapproveConfirm: false });
  };
  disAproveSubmit = values => {
    let data = {
      bid_id: this.state.disapprovedBidId,
      reason: values["reason"]
    };
    this.props
      .disapproveBuyBid(data)
      .then(res => {
        this.setState({ disapprovedBidId: "", disapproveConfirm: false });
        this.getBuySellRecievedBidList();
      })
      .catch(error => {});
  };
  viewUser = userId => {
    let data = {
      user_id: userId
    };
    this.props
      .getUserDetails(data)
      .then(res => {
        this.props.showUserDetails(true);
      })
      .catch(error => {});
  };

  viewMyOffer = id => {
    let data = {
      type: this.state.listType === 1 ? 0 : 1,
      page: 1,
      pagination: 1,
      offer_id: id,
      single: 1
    };
    this.props
      .getBuySellOfferById(data)
      .then(res => {
        this.setState({ viewModal: true });
      })
      .catch(res => {});
  };

  componentDidMount = () => {
    // get buysell list
    this.getBuySellRecievedBidList();
  };

  getBuySellRecievedBidList = () => {
    let data = {
      type: this.state.listType,
      page: this.state.buySellPage,
      pagination: 1,
      status: this.state.status
    };
    this.props
      .buySellBidsRecieved(data)
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {
        console.log(error);
      });
  };

  listTabChange = (e, data) => {
    this.setState(
      {
        defaultTab: data.activeIndex,
        defaultTabList: data.activeIndex,
        listType: data.activeIndex,
        buySellPage: 1,
        defaultFilter: "0"
      },
      () => {
        // getBuySelbid
        this.getBuySellRecievedBidList();
      }
    );
  };
  onClose = () => {
    this.props.emptyOfferViewed();
    this.setState({ viewModal: false });
  };
  pageChange = (e, data) => {
    console.log(data.activePage);
    this.setState({ buySellPage: data.activePage }, () => {
      this.getBuySellRecievedBidList();
    });
  };
  onFilter = (e, data) => {
    console.log(data.value);
    this.setState({ status: data.value }, () => {
      this.getBuySellRecievedBidList();
    });
  };
  render() {
    let { offerDetailById, handleSubmit } = this.props;
    console.log("hh", offerDetailById);
    //listing buysell
    const historyBuySell = [
      {
        menuItem: "Buy bids on your sell offer ",
        render: () => (
          <BuySellHistory
            type="BUY"
            record={this.props.myRecievedBids}
            viewMyOffer={this.viewMyOffer}
            approveBuyBid={this.approveBuyBid}
            disapproveBuyBid={this.disapproveBuyBid}
            viewUser={this.viewUser}
            sendMoney={this.sendMoney}
          />
        )
      },
      {
        menuItem: "Sell Bids on your buy offer ",
        render: () => (
          <BuySellHistory
            type="SELL"
            record={this.props.myRecievedBids}
            viewMyOffer={this.viewMyOffer}
            approveBuyBid={this.approveBuyBid}
            disapproveBuyBid={this.disapproveBuyBid}
            viewUser={this.viewUser}
            sendMoney={this.sendMoney}
          />
        )
      }
    ];

    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="My Recieved Bids" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>
        <Container className="MainContainerBid">
          <Dropdown
            placeholder="Filter"
            selectOnBlur={false}
            selection
            defaultValue={this.state.defaultFilter}
            options={filterOptions}
            onChange={(e, data) => this.onFilter(e, data)}
          />
          <div className="tabsBox tabsBoxSec">
            <Tab
              menu={{ secondary: true }}
              panes={historyBuySell}
              activeIndex={this.state.defaultTabList}
              onTabChange={(e, data) => this.listTabChange(e, data)}
            />
          </div>
          {this.props.totalBidsrecieved > 0 && (
            <div className="pageBottom">
              <Pagination
                boundaryRange={0}
                activePage={this.state.buySellPage}
                ellipsisItem={null}
                firstItem={null}
                lastItem={null}
                siblingRange={1}
                totalPages={Math.ceil(this.props.totalBidsrecieved / 10)}
                onPageChange={(e, data) => this.pageChange(e, data)}
              />
            </div>
          )}
        </Container>
        {offerDetailById.length > 0 && (
          <Modal
            className="bidConfirm"
            open={this.state.viewModal}
            onClose={this.onClose}
            centered={false}
            closeIcon
          >
            <Modal.Header>Offer Details</Modal.Header>
            <Modal.Content image>
              <List>
                <List.Item>
                  User : <span> {offerDetailById[0].email}</span>
                </List.Item>
                <List.Item>
                  Payment method :{" "}
                  <span>{offerDetailById[0].payment_type}</span>
                </List.Item>

                <List.Item>
                  Offer Price :{" "}
                  <span>
                    {smallestunitFormat(offerDetailById[0].bidprice)}{" "}
                    {offerDetailById[0].fiat_coin.toUpperCase()}/
                    {offerDetailById[0].crypto_coin.toUpperCase()}
                  </span>
                </List.Item>
                <List.Item>
                  Location : <span> {offerDetailById[0].location}</span>
                </List.Item>
                <List.Item>
                  Offer type :{" "}
                  <span> {offerDetailById[0].type === 1 ? "Sell" : "Buy"}</span>
                </List.Item>
                <List.Item>
                  Crypto coin :{" "}
                  <span> {offerDetailById[0].crypto_coin.toUpperCase()}</span>
                </List.Item>
                <List.Item>
                  Fiat coin :{" "}
                  <span> {offerDetailById[0].fiat_coin.toUpperCase()}</span>
                </List.Item>
                <List.Item>
                  Date :{" "}
                  <span>{listDateFormat(offerDetailById[0].created_at)}</span>
                </List.Item>
              </List>
            </Modal.Content>
          </Modal>
        )}

        {/* //bidConfirm model */}
        <Confirm
          open={this.state.approveConfirm}
          onCancel={this.closeApproveConfirm}
          onConfirm={this.approveConfirm}
        />

        {/* //reason model disapproved */}
        <div>
          <Modal
            size={"small"}
            closeIcon
            open={this.state.disapproveConfirm}
            onClose={this.closeDisapproveModel}
            closeOnDimmerClick={false}
            className={"EditModal"}
          >
            <Modal.Header>Post reason</Modal.Header>
            <Modal.Content>
              <div className="AddPaymentInfo">
                <Form
                  className="informationSec"
                  onSubmit={handleSubmit(this.disAproveSubmit)}
                >
                  <Form.Field>
                    <label>Cancel Reason</label>
                    <Field
                      type="text"
                      name="reason"
                      validate={[required()]}
                      component={TextAreas}
                      placeholder="Cancel reason"
                    ></Field>
                  </Form.Field>

                  <Form.Field className="addBtn">
                    <Button type="submit">Confirm</Button>
                  </Form.Field>
                </Form>
              </div>
            </Modal.Content>
          </Modal>
        </div>
        {/* send money */}
        <Modal
          size={"small"}
          closeIcon
          open={this.state.sendMoneyConfirm}
          onClose={this.closeSendMoneyModal}
          closeOnDimmerClick={false}
          className={"EditModal"}
        >
          <Modal.Header>Send Money</Modal.Header>
          <Modal.Content>
            <div className="AddPaymentInfo">
              <h4 style={{ color: "#fff" }}>
                You have to pay{" "}
                {smallestunitFormat(this.state.sendMoneyDetails.payment_amount)}{" "}
                {this.state.sendMoneyDetails.fiat_coin
                  ? this.state.sendMoneyDetails.fiat_coin.toUpperCase()
                  : this.state.sendMoneyDetails.fiat_coin}{" "}
                to following payment details:
              </h4>
              <p style={{ color: "#fff" }}>
                Payment type:{this.state.sendMoneyDetails.payment_type}
              </p>
              <p style={{ color: "#fff" }}>
                Payment Identifier:
                {this.state.sendMoneyDetails.payment_identifier}
              </p>
              <p style={{ color: "#fff" }}>
                Payment Detailed info:
                {this.state.sendMoneyDetails.detailed_info}
              </p>

              <Form
                className="informationSec"
                onSubmit={handleSubmit(this.sendMoneySubmit)}
              >
                <Form.Field>
                  <label>Transaction Id</label>
                  <Field
                    type="text"
                    name="t_id"
                    validate={[required()]}
                    component={FormField}
                    placeholder="Transaction Id"
                  ></Field>
                </Form.Field>

                <Form.Field className="addBtn">
                  <Button type="submit">Submit</Button>
                </Form.Field>
              </Form>
            </div>
          </Modal.Content>
        </Modal>
        {/* user detail compo */}

        <UserDetails></UserDetails>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    myRecievedBids: state.offer.myRecievedBids,
    totalBidsrecieved: state.offer.totalBidsRecieved,
    offerDetailById: state.offer.offerDetailById,
    showUserDetail: state.offer.showUserDetail
  };
};
const mapDispatchToProps = dispatch => {
  return {
    buySellBidsRecieved: data => dispatch(buySellBidsRecieved(data)),
    getBuySellOfferById: data => dispatch(getBuySellOfferById(data)),
    emptyOfferViewed: () => dispatch(emptyOfferViewed()),
    approveBuyBid: data => dispatch(approveBuyBid(data)),
    disapproveBuyBid: data => dispatch(disapproveBuyBid(data)),
    sendMoney: data => dispatch(sendMoney(data)),
    showUserDetails: status => dispatch(showUserDetails(status)),
    getUserDetails: data => dispatch(getUserDetails(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "disapproveForm" })(MyrecievedBids));
