import React, { PureComponent } from "react";
import {
  Container,
  Tab,
  Dropdown,
  Table,
  Breadcrumb,
  Pagination,
  Confirm
} from "semantic-ui-react";
import "./MyOffer.scss";
import PageTitle from "../../../Components/PageTitle/PageTitle";

import { Field, reduxForm, reset } from "redux-form";
import { required, email } from "redux-form-validators";
import { connect } from "react-redux";
import { getBuySellOffer, cancelOffer } from "../../../redux/reducers/offer";
import {
  getCryptoActiveCoins,
  getFiatActiveCoins
} from "../../../redux/reducers/currency";
import { smallestunitFormat } from "../../../Utils/helper";
const filterOptions = [
  {
    key: "1",
    text: "Pending",
    value: "0"
  },
  {
    key: "2",
    text: "Approved",
    value: "1"
  },
  {
    key: "3",
    text: "Disapproved",
    value: "2"
  },

  {
    key: "4",
    text: "Cancelled",
    value: "3"
  },
  {
    key: "5",
    text: "Claimed",
    value: "4"
  }
];

const BuySellHistory = ({ type, record, cancelMyOrder }) => {
  console.log("data", record);

  return (
    <Tab.Pane attached={false}>
      <Table inverted selectable>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>
              {type === "BUY" ? "Seller" : "Buyer"}
            </Table.HeaderCell>
            <Table.HeaderCell>Payment method</Table.HeaderCell>
            <Table.HeaderCell>Amount</Table.HeaderCell>
            <Table.HeaderCell>Limits</Table.HeaderCell>
            <Table.HeaderCell>Status</Table.HeaderCell>

            <Table.HeaderCell>Action</Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {record.map((data, index) => (
            <Table.Row key={index}>
              <Table.Cell>
                <a href>{data.email}</a>{" "}
              </Table.Cell>
              <Table.Cell>{data.payment_type}</Table.Cell>
              <Table.Cell>
                <a href>{smallestunitFormat(data.amount)} USD</a>{" "}
              </Table.Cell>
              <Table.Cell>
                {smallestunitFormat(data.min_transaction)} -{" "}
                {smallestunitFormat(data.max_transaction)} USD{" "}
              </Table.Cell>
              <Table.Cell>
                {data.status === 0 && data.partial === 0
                  ? "Pending"
                  : data.status === 1 && data.partial === 0
                  ? "Approved"
                  : data.status === 2 && data.partial === 0
                  ? "Disapproved"
                  : data.status === 3 && data.partial === 0
                  ? "Cancelled"
                  : data.status === 4 && data.partial === 0
                  ? "Claimed"
                  : data.status === 0 && data.partial === 1
                  ? "Partially open"
                  : data.status === 3 && data.partial === 1
                  ? "Partially cancelled"
                  : ""}
              </Table.Cell>
              {data.status === 0 && (
                <Table.Cell>
                  <a href className="btn" onClick={() => cancelMyOrder(data.o_id)}>
                    Cancel offer
                  </a>
                </Table.Cell>
              )}
            </Table.Row>
          ))}
          {record.length === 0 && (
            <Table.Row>
              <Table.Cell style={{ textAlign: "center" }} Colspan="6">
                No records found
              </Table.Cell>
            </Table.Row>
          )}
        </Table.Body>
      </Table>
    </Tab.Pane>
  );
};

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "My offer", active: true }
];

class MyOffer extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      defaultTab: 0,
      defaultTabList: 0,
      listType: 0,
      buySellPage: 1,
      open: false,
      cancelOfferId: "",
      defaultFilter: "0"
    };
  }

  componentDidMount = () => {
    // get buysell list
    this.getBuySellList();
  };

  open = id => {
    this.setState({ open: true, cancelOfferId: id });
  };
  close = () => this.setState({ open: false });
  cancelMyOrder = () => {
    let data = {
      offerId: this.state.cancelOfferId
    };
    this.props
      .cancelOffer(data)
      .then(res => {
        this.setState({ open: false });
        this.getBuySellList();
      })
      .catch(errro => {});
  };

  getBuySellList = () => {
    let data = {
      type: this.state.listType,
      page: this.state.buySellPage,
      pagination: 1,
      status: this.state.status,

      self: 1
    };
    this.props
      .getBuySellOffer(data)
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {
        console.log(error);
      });
  };

  listTabChange = (e, data) => {
    this.setState(
      {
        defaultTab: data.activeIndex,
        defaultTabList: data.activeIndex,
        listType: data.activeIndex,
        buySellPage: 1
      },
      () => {
        // getBuySellList
        this.getBuySellList();
      }
    );
  };
  pageChange = (e, data) => {
    console.log(data.activePage);
    this.setState({ buySellPage: data.activePage }, () => {
      this.getBuySellList();
    });
  };
  onFilter = (e, data) => {
    console.log(data.value);
    this.setState({ status: data.value }, () => {
      this.getBuySellList();
    });
  };
  render() {
    //listing buysell
    const historyBuySell = [
      {
        menuItem: "Buy Offers ",
        render: () => (
          <BuySellHistory
            type="BUY"
            record={this.props.buySellList}
            cancelMyOrder={this.open}
          />
        )
      },
      {
        menuItem: "Sell Offers ",
        render: () => (
          <BuySellHistory
            type="SELL"
            record={this.props.buySellList}
            cancelMyOrder={this.open}
          />
        )
      }
    ];

    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="My Offers" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>
        <Container className="MainContainerBid">
          <Dropdown
            placeholder="Filter"
            selectOnBlur={false}
            selection
            defaultValue={this.state.defaultFilter}
            options={filterOptions}
            onChange={(e, data) => this.onFilter(e, data)}
          />
          <div className="tabsBox tabsBoxSec">
            <Tab
              menu={{ secondary: true }}
              panes={historyBuySell}
              activeIndex={this.state.defaultTabList}
              onTabChange={(e, data) => this.listTabChange(e, data)}
            />
          </div>
          {this.props.totalOffer && (
            <div className="pageBottom">
              <Pagination
                boundaryRange={0}
                activePage={this.state.buySellPage}
                ellipsisItem={null}
                firstItem={null}
                lastItem={null}
                siblingRange={1}
                totalPages={Math.ceil(this.props.totalOffer / 10)}
                onPageChange={(e, data) => this.pageChange(e, data)}
              />
            </div>
          )}
        </Container>
        {/* cancle offer confirmation */}
        <div>
          <Confirm
            open={this.state.open}
            onCancel={this.close}
            onConfirm={this.cancelMyOrder}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    buySellList: state.offer.buySellOfferSelf,
    totalOffer: state.offer.totalOfferSelf
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getBuySellOffer: data => dispatch(getBuySellOffer(data)),
    getCryptoActiveCoins: () => dispatch(getCryptoActiveCoins()),
    getFiatActiveCoins: () => dispatch(getFiatActiveCoins()),
    cancelOffer: data => dispatch(cancelOffer(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferForm" })(MyOffer));
