import React, { useEffect, useState } from "react";
import { Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { checkAuthToken } from "../../redux/reducers/user";
import { LOGIN_URL } from "../../constant";

const AuthGuard = ({ component: Component, ...rest }) => {
  const { token } = rest;
  const [path, setPath] = useState("");

  var isAuthenticated = false;

  useEffect(() => {
    let { location, checkAuthToken } = rest;
    if (location) {
      setPath(location);
      if (path !== location)
        checkAuthToken({ token: localStorage.getItem("JwtToken") });
    }
  }, [rest, path, token]);

  if (localStorage.getItem("JwtToken")) {
    //  localStorage.setItem(
    //    "JwtToken",
    //    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqd3REYXRhIjoiZmM5MGViYTMtM2U4OS0xMWVhLWFhYmMtMDAwYzI5ZmYwMmNhIiwiaWF0IjoxNTg2MTQ2MzM2LCJleHAiOjE1ODYyMzI3MzZ9.fIYpyHMnLS43cqdjcfpTMMbd5HILGcnoYTBevFIM4bQ"
    //  );
    isAuthenticated = true;
  } else {
    //  localStorage.setItem(
    //    "JwtToken",
    //    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqd3REYXRhIjoiZmM5MGViYTMtM2U4OS0xMWVhLWFhYmMtMDAwYzI5ZmYwMmNhIiwiaWF0IjoxNTg2MTQ2MzM2LCJleHAiOjE1ODYyMzI3MzZ9.fIYpyHMnLS43cqdjcfpTMMbd5HILGcnoYTBevFIM4bQ"
    //  );
    // isAuthenticated = true;
    // window.open("http://10.1.0.71:4200/", "_self");
    window.open(`${LOGIN_URL}`, "_self");
  }

  return (
    <Route
      {...rest}
      render={(props) =>
        isAuthenticated ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: "/",
              state: {
                from: props.location,
              },
            }}
          />
        )
      }
    />
  );
};

const mapStateToProps = (state) => {
  return {
    token: false,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    checkAuthToken: (data) => dispatch(checkAuthToken(data)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(AuthGuard);
