import React, { Component } from "react";
import "../SellBitcoin/SellBitcoin.scss";
import {
  Container,
  Form,
  Input,
  Select,
  Button,
  Breadcrumb,
  Grid,
  TextArea,
  List,
} from "semantic-ui-react";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import infoIcon from "../../../images/info.png";
import { Field, reduxForm, reset, change } from "redux-form";
import { required, numericality } from "redux-form-validators";
import { connect } from "react-redux";
import { getBuySellOfferById } from "../../../redux/reducers/offer";
//import io from "socket.io-client";
import { startStopLoading } from "../../../redux/reducers/loading";

import { SOCKET_URL, rootName } from "../../../constant";
import { smallestunitFormat } from "../../../Utils/helper";
import { FormField, TextAreas } from "../../../Components/Common";
import { toast } from "../../../Components/Toast/Toast";
import { saveBuyOfferDetail } from "../../../redux/reducers/persist";
import { socket } from "../../../App";

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "Buy Bitcoin", active: true },
];

export class BuyBitcoin extends Component {
  componentDidMount = () => {
    // alert(this.props.location.state.id);

    window.scrollTo(0, 0);

    this.checkIfPageAccessible();

    //socket block listner
    socket.on("block", (res) => {
      console.log("BLOCKED ID", res);
      console.log("SELECTED ID", this.props.selectedOfferDetail.offer.o_id);
      this.props.startStopLoading(false);

      if (res === this.props.selectedOfferDetail.offer.o_id) {
        this.props.history.push(`${rootName}/auth/MakePayment`);
      }
    });

    //already offer Frizzed
    socket.on("already_block", (res) => {
      if (res === this.props.selectedOfferDetail.offer.o_id) {
        toast.error("Someone is already freezed this offer!");
      }
    });
  };
  checkIfPageAccessible = () => {
    if (Object.keys(this.props.selectedOfferDetail).length === 0) {
      this.props.history.push(`${rootName}/`);
    }
  };

  quantityChanged = (e) => {
    let { selectedOfferDetail } = this.props;

    if (e.target.value > 0) {
      var quantity =
        e.target.value / smallestunitFormat(selectedOfferDetail.offer.amount);

      this.props.dispatch(
        change("OfferBidForm", "quantity", quantity.toFixed(8))
      );
    } else {
      this.props.dispatch(change("OfferBidForm", "quantity", ""));
    }
  };
  sumbitForm = (values) => {
    let { selectedOfferDetail, saveBuyOfferDetail } = this.props;
    if (
      values["amount"] <
      smallestunitFormat(selectedOfferDetail.offer.min_transaction)
    ) {
      toast.error(
        `Minimum amount should be greater or equal to ${smallestunitFormat(
          selectedOfferDetail.offer.min_transaction
        )} ${selectedOfferDetail.offer.fiat_coin.toUpperCase()}`
      );
    }
    if (
      values["amount"] >
      smallestunitFormat(selectedOfferDetail.offer.max_transaction)
    ) {
      toast.error(
        `Maximum amount should be less or equal to ${smallestunitFormat(
          selectedOfferDetail.offer.max_transaction
        )} ${selectedOfferDetail.offer.fiat_coin.toUpperCase()}`
      );
    }

    if (
      values["amount"] >=
        smallestunitFormat(selectedOfferDetail.offer.min_transaction) &&
      values["amount"] <=
        smallestunitFormat(selectedOfferDetail.offer.max_transaction)
    ) {
      this.props.saveBuyOfferDetail(values);
      socket.emit("disableOffer", { offer_id: selectedOfferDetail.offer.o_id });

      //   this.props.history.push(`${rootName}/auth/MakePayment`);
    }
  };

  render() {
    let { selectedOfferDetail, handleSubmit } = this.props;
    if (Object.keys(this.props.selectedOfferDetail).length !== 0) {
      return (
        <div>
          <Container fluid>
            <Container className="TopHeader">
              <PageTitle title="Buy Bitcoin" link="Back" />
            </Container>
          </Container>
          <Container fluid className="TopBreadcrumb">
            <Container>
              <Breadcrumb icon="right angle" sections={sections} />
            </Container>
          </Container>
          <Container className="MainContainerBuy">
            <Grid divided="vertically" doubling columns={2}>
              <Grid.Row>
                <Grid.Column>
                  <div className="sellBoxes">
                    <div className="sellBoxesHead">
                      <h3>Buy bitcoins using UPI Gpay with INR </h3>
                    </div>
                    <div className="sellBoxesForm">
                      <p>
                        An-Exchange{" "}
                        <span>{selectedOfferDetail.offer.email}</span> wishes to
                        sell bitcoins to you.{" "}
                      </p>
                      <List>
                        <List.Item>
                          Price:{" "}
                          <span>
                            {smallestunitFormat(
                              selectedOfferDetail.offer.amount
                            )}
                            {this.props.selectedOfferDetail.offer.fiat_coin.toUpperCase()}{" "}
                            / BTC
                          </span>
                        </List.Item>
                        <List.Item>
                          Payment method{" "}
                          <span>
                            {selectedOfferDetail.offer.payment_type}{" "}
                            <img src={infoIcon} width="17" height="17" alt="infoIcon"/>{" "}
                          </span>
                        </List.Item>
                        <List.Item>
                          User{" "}
                          <span>
                            {" "}
                            {selectedOfferDetail.offer.email} <br />
                            <b>(feedback score 99 %, see feedback)</b>
                          </span>
                        </List.Item>
                        <List.Item>
                          Trade limits{" "}
                          <span>
                            {" "}
                            {smallestunitFormat(
                              selectedOfferDetail.offer.min_transaction
                            )}{" "}
                            -{" "}
                            {smallestunitFormat(
                              selectedOfferDetail.offer.max_transaction
                            )}{" "}
                            {this.props.selectedOfferDetail.offer.fiat_coin.toUpperCase()}{" "}
                          </span>
                        </List.Item>
                        <List.Item>
                          Location{" "}
                          <span>{selectedOfferDetail.offer.location}</span>
                        </List.Item>
                        <List.Item>
                          Payment window{" "}
                          <span>
                            5 minutes{" "}
                            <img src={infoIcon} width="17" height="17" alt="iconInfo"/>{" "}
                          </span>
                        </List.Item>
                      </List>
                      <Form
                        className="sellForm"
                        onSubmit={handleSubmit(this.sumbitForm)}
                      >
                        <div className="sellFormTop">
                          <Form.Field>
                            <label>How much you wish to buy? </label>
                            <div className="sellFormTopInput">
                              <div className="sellFormInput">
                                <Field
                                  name="amount"
                                  type="text"
                                  label={this.props.selectedOfferDetail.offer.fiat_coin.toUpperCase()}
                                  placeholder="0.00"
                                  labelPosition="right"
                                  component={FormField}
                                  validate={[
                                    required(),
                                    numericality({ ">": 0 }),
                                  ]}
                                  onChange={(e) => this.quantityChanged(e)}
                                ></Field>
                              </div>
                              <div className="sellFormInput">
                                <Field
                                  readOnly
                                  name="quantity"
                                  type="text"
                                  placeholder="0.0000"
                                  label="BTC"
                                  labelPosition="right"
                                  component={FormField}
                                ></Field>
                              </div>
                            </div>
                          </Form.Field>
                          <Form.Field>
                            <Field
                              name="other_info"
                              type="text"
                              placeholder="Write your contact message and other information to 
                              the trader here... (optional)"
                              component={TextAreas}
                            ></Field>
                          </Form.Field>
                        </div>
                        <Button type="submit">Make Payment</Button>
                      </Form>
                    </div>
                  </div>
                </Grid.Column>
                <Grid.Column>
                  <div className="sellBoxes">
                    <div className="sellBoxesHead">
                      <h3>
                        Terms of trade with{" "}
                        <span>{selectedOfferDetail.offer.email}</span>
                      </h3>
                    </div>
                    <div className="sellBoxesInfo">
                      <p>{selectedOfferDetail.offer.terms}</p>
                    </div>
                  </div>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Container>
        </div>
      );
    } else {
      return <div></div>;
    }
  }
}

const mapStateToProps = (state) => {
  console.log("SATTE REDUX", state);
  return {
    selectedOfferDetail: state.persist.selectedOfferDetail,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getBuySellOfferById: (data) => dispatch(getBuySellOfferById(data)),
    saveBuyOfferDetail: (data) => dispatch(saveBuyOfferDetail(data)),
    startStopLoading: (status) => dispatch(startStopLoading(status)),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferBidForm", destroyOnUnmount: false })(BuyBitcoin));
