import React, { Component } from "react";
import "./SellBitcoin.scss";
import {
  Container,
  Form,
  Button,
  Breadcrumb,
  Grid,
  List
} from "semantic-ui-react";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import infoIcon from "../../../images/info.png";
import { Field, reduxForm, reset, change } from "redux-form";
import { required, numericality } from "redux-form-validators";
import { connect } from "react-redux";
import { getBuySellOfferById } from "../../../redux/reducers/offer";
import { startStopLoading } from "../../../redux/reducers/loading";
import { getWalletBalance } from "../../../redux/reducers/currency";

import { SOCKET_URL, rootName } from "../../../constant";
import { smallestunitFormat } from "../../../Utils/helper";
import { FormField, TextAreas } from "../../../Components/Common";
import { toast } from "../../../Components/Toast/Toast";
import { saveSellOfferDetail } from "../../../redux/reducers/persist";
import { socket } from "../../../App";

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "Sell Bitcoin", active: true }
];

export class SellBitcoin extends Component {
  componentWillUnmount = () => {
    // socket.disconnect();
  };
  getWalletBalance = () => {
    this.props
      .getWalletBalance()
      .then(res => {})
      .catch(error => {});
  };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    //wallet balnce
    this.getWalletBalance();

    this.checkIfPageAccessible();

    //socket block listner
    socket.on("block", res => {
      console.log("BLOCKED ID", res);
      console.log("SELECTED ID", this.props.selectedOfferDetailSell.offer.o_id);
      this.props.startStopLoading(false);

      if (res === this.props.selectedOfferDetailSell.offer.o_id) {
        this.props.history.push(`${rootName}/auth/MakePaymentSell`);
      }
    });

    //already offer Frizzed
    socket.on("already_block", res => {
      if (res === this.props.selectedOfferDetailSell.offer.o_id) {
        toast.error("Someone is already freezed this offer!");
      }
    });
  };
  checkIfPageAccessible = () => {
    if (Object.keys(this.props.selectedOfferDetailSell).length === 0) {
      this.props.history.push(`${rootName}/`);
    }
  };

  btcQuantityChanged = e => {
    let { selectedOfferDetailSell } = this.props;

    if (e.target.value > 0) {
      var amount =
        smallestunitFormat(selectedOfferDetailSell.offer.amount) *
        e.target.value;

      this.props.dispatch(change("OfferBidForm", "amount", amount.toFixed(4)));
    } else {
      this.props.dispatch(change("OfferBidForm", "amount", ""));
      this.props.dispatch(change("OfferBidForm", "quantity", ""));
    }
  };

  amountQuantityChanged = e => {
    let { selectedOfferDetailSell } = this.props;
    if (e.target.value > 0) {
      var quantity =
        e.target.value /
        smallestunitFormat(selectedOfferDetailSell.offer.amount);

      this.props.dispatch(
        change("OfferBidForm", "quantity", quantity.toFixed(4))
      );
    } else {
      this.props.dispatch(change("OfferBidForm", "quantity", ""));
      this.props.dispatch(change("OfferBidForm", "amount", ""));
    }
  };
  sumbitForm = values => {
    let { selectedOfferDetailSell, saveSellOfferDetail } = this.props;
    if (
      values["amount"] <
      smallestunitFormat(selectedOfferDetailSell.offer.min_transaction)
    ) {
      toast.error(
        `Minimum amount should be greater or equal to ${smallestunitFormat(
          selectedOfferDetailSell.offer.min_transaction
        )} ${selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}`
      );
    }
    if (
      values["amount"] >
      smallestunitFormat(selectedOfferDetailSell.offer.max_transaction)
    ) {
      toast.error(
        `Maximum amount should be less or equal to ${smallestunitFormat(
          selectedOfferDetailSell.offer.max_transaction
        )} ${selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}`
      );
    }

    if (
      values["amount"] >=
        smallestunitFormat(selectedOfferDetailSell.offer.min_transaction) &&
      values["amount"] <=
        smallestunitFormat(selectedOfferDetailSell.offer.max_transaction)
    ) {
      this.props.walletBalance.forEach(element => {
        if (element.name === selectedOfferDetailSell.offer.crypto_coin) {
          if (values["quantity"] <= smallestunitFormat(element.balance)) {
            this.props.saveSellOfferDetail(values);
            socket.emit("disableOffer", {
              offer_id: selectedOfferDetailSell.offer.o_id
            });
          } else {
            toast.error("Insufficient balance");
          }
        }
      });
      console.log("Wallet balance", this.props.walletBalance);
      console.log("CRYPTO", selectedOfferDetailSell.offer.crypto_coin);
    }
  };

  render() {
    let { selectedOfferDetailSell, handleSubmit } = this.props;
    if (Object.keys(this.props.selectedOfferDetailSell).length !== 0) {
      return (
        <div>
          <Container fluid>
            <Container className="TopHeader">
              <PageTitle
                title={`Sell ${this.props.selectedOfferDetailSell.offer.crypto_coin.toUpperCase()}`}
                link="Back"
              />
            </Container>
          </Container>
          <Container fluid className="TopBreadcrumb">
            <Container>
              <Breadcrumb icon="right angle" sections={sections} />
            </Container>
          </Container>
          <Container className="MainContainerBuy">
            <Grid divided="vertically" doubling columns={2}>
              <Grid.Row>
                <Grid.Column>
                  <div className="sellBoxes">
                    <div className="sellBoxesHead">
                      <h3>
                        Sell{" "}
                        {this.props.selectedOfferDetailSell.offer.crypto_coin.toUpperCase()}{" "}
                        using UPI Gpay with{" "}
                        {this.props.selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}{" "}
                      </h3>
                    </div>
                    <div className="sellBoxesForm">
                      <p>
                        An-Exchange{" "}
                        <span>{selectedOfferDetailSell.offer.email}</span>{" "}
                        wishes to buy{" "}
                        {this.props.selectedOfferDetailSell.offer.crypto_coin.toUpperCase()}{" "}
                        .{" "}
                      </p>
                      <List>
                        <List.Item>
                          Price:{" "}
                          <span>
                            {smallestunitFormat(
                              selectedOfferDetailSell.offer.amount
                            )}
                            {this.props.selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}{" "}
                            / BTC
                          </span>
                        </List.Item>
                        <List.Item>
                          Payment method{" "}
                          <span>
                            {selectedOfferDetailSell.offer.payment_type}{" "}
                            <img src={infoIcon} width="17" height="17" alt="infoIcon"/>{" "}
                          </span>
                        </List.Item>
                        <List.Item>
                          User{" "}
                          <span>
                            {" "}
                            {selectedOfferDetailSell.offer.email} <br />
                            <b>(feedback score 99 %, see feedback)</b>
                          </span>
                        </List.Item>
                        <List.Item>
                          Trade limits{" "}
                          <span>
                            {" "}
                            {smallestunitFormat(
                              selectedOfferDetailSell.offer.min_transaction
                            )}{" "}
                            -{" "}
                            {smallestunitFormat(
                              selectedOfferDetailSell.offer.max_transaction
                            )}{" "}
                            {this.props.selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}{" "}
                          </span>
                        </List.Item>
                        <List.Item>
                          Location{" "}
                          <span>{selectedOfferDetailSell.offer.location}</span>
                        </List.Item>
                        <List.Item>
                          Payment window{" "}
                          <span>
                            5 minutes{" "}
                            <img src={infoIcon} width="17" height="17" alt="infoIcon"/>{" "}
                          </span>
                        </List.Item>
                      </List>
                      <Form
                        className="sellForm"
                        onSubmit={handleSubmit(this.sumbitForm)}
                      >
                        <div className="sellFormTop">
                          <Form.Field>
                            <label>How much you wish to sell? </label>
                            <div className="sellFormTopInput">
                            <div className="sellFormInput">
                              <Field
                                name="quantity"
                                type="text"
                                placeholder="0.0000"
                                label="BTC"
                                labelPosition="right"
                                component={FormField}
                                validate={[
                                  required(),
                                  numericality({ ">": 0 })
                                ]}
                                onChange={e => this.btcQuantityChanged(e)}
                              ></Field></div>
                              <div className="sellFormInput">
                              <Field
                                name="amount"
                                type="text"
                                label={this.props.selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}
                                placeholder="0.00"
                                labelPosition="right"
                                component={FormField}
                                validate={[
                                  required(),
                                  numericality({ ">": 0 })
                                ]}
                                onChange={e => this.amountQuantityChanged(e)}
                              ></Field></div>
                            </div>
                          </Form.Field>
                          <Form.Field>
                            <Field
                              name="other_info"
                              type="text"
                              placeholder="Write your contact message and other information to 
                              the trader here... (optional)"
                              component={TextAreas}
                            ></Field>
                          </Form.Field>
                        </div>
                        <Button type="submit">Next</Button>
                      </Form>
                    </div>
                  </div>
                </Grid.Column>
                <Grid.Column>
                  <div className="sellBoxes">
                    <div className="sellBoxesHead">
                      <h3>
                        Terms of trade with{" "}
                        <span>{selectedOfferDetailSell.offer.email}</span>
                      </h3>
                    </div>
                    <div className="sellBoxesInfo">
                      <p>{selectedOfferDetailSell.offer.terms}</p>
                    </div>
                  </div>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Container>
        </div>
      );
    } else {
      return <div></div>;
    }
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    selectedOfferDetailSell: state.persist.selectedOfferDetailSell,
    walletBalance: state.currency.walletBalance
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getBuySellOfferById: data => dispatch(getBuySellOfferById(data)),
    saveSellOfferDetail: data => dispatch(saveSellOfferDetail(data)),
    startStopLoading: status => dispatch(startStopLoading(status)),
    getWalletBalance: () => dispatch(getWalletBalance())
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferBidForm" })(SellBitcoin));
