import React, { Component } from "react";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import {
  Container,
  Breadcrumb,
  Grid,
  List,
  Button,
  Form,
  Select
} from "semantic-ui-react";
import { Field, reduxForm, reset, change } from "redux-form";
import { required, email } from "redux-form-validators";
import { connect } from "react-redux";
import { FormField, SelectField } from "../../../Components/Common/index";
import { getUserPaymentMethods } from "../../../redux/reducers/currency";
import "./MakePayment.scss";
import { buySellOffer } from "../../../redux/reducers/offer";
import { listDateFormat, smallestunitFormat } from "../../../Utils/helper";
//import io from "socket.io-client";
import { socket } from "../../../App";

import { SOCKET_URL, rootName } from "../../../constant";
// let socket = io.connect(SOCKET_URL, {
//   reconnect: true,
//   transports: ["websocket"]
// });
const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Sell", content: "Sell Bitcoin", link: true },
  { key: "confirm", content: "Sell Bitcoin", active: true }
];

export class MakePayment extends Component {
  componentWillUnmount = () => {
    console.log(this.props.history);
    if (this.props.history.location.pathname === `${rootName}/auth/BuyBitcoin`) {
      //your code... on back button
      socket.emit("unblocked_single", {
        offer_id: this.props.selectedOfferDetail.offer.o_id
      });
    }
  };
  componentDidMount = () => {
    // socket unblock listner

    socket.on("unblocked", res => {
      //   this.props.history.push(`${rootName}`);
    });
    if (Object.keys(this.props.saveBuyDetails).length !== 0) {
      this.getUserPayementMethod();
    } else {
      this.props.history.push(`${rootName}/`);
    }
  };
  getUserPayementMethod = () => {
    this.props
      .getUserPaymentMethods()
      .then(res => {})
      .catch(error => {});
  };
  sumbitForm = values => {
    let { selectedOfferDetail, saveBuyDetails } = this.props;
    var data = {
      offer_id: selectedOfferDetail.offer.o_id,
      bid_type: selectedOfferDetail.offer.type === 1 ? 0 : 1,
      quantity: saveBuyDetails.quantity,
      amount: saveBuyDetails.amount,
      other_info: saveBuyDetails.other_info,

      trans_id: values["transaction_Id"],
      payment_id: values["paymentId"]
    };

    this.props
      .buySellOffer(data)
      .then(res => {
        if (Object.keys(this.props.selectedOfferDetail).length !== 0) {
          socket.emit("unblocked_single", {
            offer_id: this.props.selectedOfferDetail.offer.o_id
          });
        }
        this.props.history.push(`${rootName}/auth/myBids`);
      })
      .catch(error => {});
  };
  render() {
    let { selectedOfferDetail, handleSubmit, saveBuyDetails } = this.props;
    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="Buy Bitcoin" link="Back" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>

        <Container>
          <div className="PaymentSec">
            <h2>Seller Matched Make Payment!</h2>
            <h3>
              Offer Price{" "}
              <span>
                {smallestunitFormat(selectedOfferDetail.offer.amount)}{" "}
                {selectedOfferDetail.offer.fiat_coin.toUpperCase()} of 1 BTC
              </span>
            </h3>
            {/* <h4>Placed on Jan 17, 2020 | 3:50pm</h4>created_at */}
            <h4>
              Placed on{" "}
              {listDateFormat(selectedOfferDetail.offer.created_at, 1)}
            </h4>
            <Form onSubmit={handleSubmit(this.sumbitForm)}>
              <div className="PaymentSecIn">
                <h4>Seller payment information</h4>
                <div className="paymentInfo">
                  <List>
                    <List.Item>
                      Payment type :{" "}
                      <span> {selectedOfferDetail.offer.payment_type}</span>
                    </List.Item>
                    <List.Item>
                      Payment Indentifier :{" "}
                      <span>
                        {" "}
                        {selectedOfferDetail.offer.payment_identifier}
                      </span>
                    </List.Item>
                    <List.Item>
                      Detailed Info :{" "}
                      <span> {selectedOfferDetail.offer.detailed_info}</span>
                    </List.Item>
                  </List>
                </div>
                <div className="paymentInfo">
                  <h4>Buyer payement information</h4>

                  <Form.Field>
                    <label>Transaction Id</label>
                    {/* <input placeholder="First Name" /> */}
                    <Field
                      name="transaction_Id"
                      type="text"
                      component={FormField}
                      placeholder="Transaction Id"
                      validate={[required()]}
                    />
                  </Form.Field>
                  <Form.Field>
                    <label>Debited from</label>
                    <Field
                      name="paymentId"
                      validate={[required()]}
                      component={SelectField}
                      children={this.props.paymentMethods}
                      placeholder="Payment method"
                      selectOnBlur={false}
                    ></Field>
                  </Form.Field>
                </div>
              </div>
              <Grid divided="vertically" className="PaymentSecBot">
                <Grid.Row columns={2}>
                  <Grid.Column className="PaymentSecLeft">
                    <p>Pay Exactly</p>
                    <p className="fund">
                      {this.props.saveBuyDetails.amount}{" "}
                      {this.props.selectedOfferDetail.offer.fiat_coin.toUpperCase()}
                    </p>
                    <i>(Do not round off)</i>
                  </Grid.Column>
                  <Grid.Column className="PaymentSecRight">
                    <Button type="submit">Send Buy Trade Request</Button>
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            </Form>
          </div>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    selectedOfferDetail: state.persist.selectedOfferDetail,
    paymentMethods: state.currency.paymentMethods,
    saveBuyDetails: state.persist.saveBuyDetails
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getUserPaymentMethods: () => dispatch(getUserPaymentMethods()),
    buySellOffer: data => dispatch(buySellOffer(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferBidFormStep2" })(MakePayment));
