import React, { Component } from "react";
import "./Header.scss";
import { Container, List, Grid, Dropdown, /* Modal */ } from "semantic-ui-react";
import profile from "../../images/profile1.png";
import { withRouter } from "react-router";
import { rootName } from "../../constant";
//import { deleteCookie } from "../../Utils/helper";
import { getKyc } from "../../redux/reducers/user";
import { connect } from "react-redux";
import { startStopLoading } from "../../redux/reducers/loading";
import { LOGIN_URL } from "../../constant";
export class Header extends Component {
  componentDidMount = () => {
    this.getUserProfile();
  };
  getUserProfile = () => {
    this.props
      .getKyc()
      .then((res) => {})
      .catch((error) => {});
  };
  gotToRoute = (route) => {
    this.props.history.push(route);
  };
  logoutMe = () => {
    this.props.startStopLoading(true);
    // deleteCookie("JwtTokenTemp", "", 1);
    // deleteCookie("JwtToken", "", 1);
    document.cookie =
      '${JwtToken}=; Path=/; Expires=Thu, 30 march 1970 00:00:01 GMT;';

    document.cookie =
      '${JwtTokenTemp}=; Path=/; Expires=Thu, 30 march 1970 00:00:01 GMT;';

    setTimeout(() => {
      this.props.startStopLoading(false);

      localStorage.removeItem("JwtToken");
      //   window.open("http://10.1.0.71:4200/", "_self");
       window.open(`${LOGIN_URL}`, "_self");

     // window.open("https://p2p.coindbs.xyz/#/", "_self");
    }, 1000);
  };
  render() {
    console.log("HEADER PROPS", this.props);
    return (
      <Container fluid className="headerBg">
        <Grid>
          <Grid.Row>
            <Grid.Column width={8}>
              <a href={`${rootName}`} className="logo"></a>
            </Grid.Column>
            <Grid.Column width={8} className="RightMenu">
              <List className="HeaderNav">
                <List.Item
                  className={
                    this.props.location.pathname === `${rootName}/` ||
                    this.props.location.pathname ===
                      `${rootName}/auth/BuyBitcoin`
                      ? "active"
                      : ""
                  }
                  onClick={() => this.gotToRoute(`${rootName}`)}
                >
                  Buy/Sell Bitcoin
                </List.Item>

                <List.Item
                  className={
                    this.props.location.pathname ===
                    `${rootName}/auth/PostTrade`
                      ? "active"
                      : ""
                  }
                  onClick={() => this.gotToRoute(`${rootName}/auth/PostTrade`)}
                >
                  Post a Trade
                </List.Item>
                <List.Item
                  onClick={() =>
                     window.open(`${LOGIN_URL}#/exchange/btc_usdt`, "_self")
                   // window.open("https://p2p.coindbs.xyz/#/exchange/btc_usdt","_self")
                  }
                >
                  Exchange
                </List.Item>
              </List>
              <img src={profile} className="userprofileImg" alt="profile"/>
              <Dropdown
                className="userprofile"
                text={this.props.userProfile.email}
              >
                <Dropdown.Menu>
                  <Dropdown.Item
                    onClick={() => this.gotToRoute(`${rootName}/auth/myOffer`)}
                    text="My offers"
                  />
                  <Dropdown.Item
                    onClick={() => this.gotToRoute(`${rootName}/auth/myBids`)}
                    text="My Sent Bids"
                  />
                  <Dropdown.Item
                    onClick={() =>
                      this.gotToRoute(`${rootName}/auth/myRecievedBids`)
                    }
                    text="My Recieved Bids"
                  />
                  <Dropdown.Item
                    onClick={() =>
                      this.gotToRoute(`${rootName}/auth/AddPayment`)
                    }
                    text="Payement Method"
                  />

                  <Dropdown.Item
                    text="Logout"
                    onClick={() => this.logoutMe()}
                  />
                </Dropdown.Menu>
              </Dropdown>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    userProfile: state.user.userProfile,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    getKyc: (data) => dispatch(getKyc(data)),
    startStopLoading: (status) => dispatch(startStopLoading(status)),
  };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header));
