import React, { Component } from "react";
import "./PostTrade.scss";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import {
  getCryptoActiveCoins,
  getFiatActiveCoins,
  getUserPaymentMethods,
  getWalletBalance,
  getCryptoConvertedPrice
} from "../../../redux/reducers/currency";
import { smallestunitFormat } from "../../../Utils/helper";

import { Field, reduxForm, reset, change } from "redux-form";
import { required, numericality, length } from "redux-form-validators";
import { connect } from "react-redux";
import {
  FormField,
  SelectField,
  TextAreas
} from "../../../Components/Common/index";
import {
  Container,
  Breadcrumb,
  Form,
  Input,
  Radio,
  Checkbox,
  Grid,
  Select,
  List,
  Button
} from "semantic-ui-react";
import { startTime } from "../../../config/Constants";
import { amountCheckCrypto, amountCheckUsd } from "../../../Utils/helper";
import { createOffer } from "../../../redux/reducers/offer";
import { rootName } from "../../../constant";
import { toast } from "../../../Components/Toast/Toast";
import { marginLimitCheck } from "../../../validators/customValidators";
import { PlaceApi } from "../../../Components/Common/PlaceApi";
import {
  geocodeByAddress,
  geocodeByPlaceId,
  getLatLng
} from "react-places-autocomplete";
const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Sell", content: "Post a Trade", active: true }
];

export class PostTrade extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isHoursSelected: false,
      address: "",
      isSell: true,
      defaultFiat: "",
      selectedFiatName: "",
      defaultCrypto: "",
      selectedCryptoName: "",
      liquidityCheck: false,
      identifiedPeopleCheck: false,
      smsVerificationCheck: false,
      TrustedPeopleCheck: false,
      btcInUsd: "",
      calculatedValue: "",
      addedMargin: ""
    };
  }
  quantityChanged = e => {
    if (e.target.value > 0) {
      this.setState({ calculatedValue: this.state.btcInUsd * e.target.value });
      // calculating and assigning value for price equation
      this.props.dispatch(
        change(
          "PostTradeForm",
          "equation",
          (this.state.btcInUsd * e.target.value).toFixed(4)
        )
      );
      this.props.dispatch(
        change(
          "PostTradeForm",
          "maxTrans",
          (this.state.btcInUsd * e.target.value).toFixed(4)
        )
      );
    } else {
      this.setState({ calculatedValue: this.state.btcInUsd });
      this.props.dispatch(change("PostTradeForm", "equation", ""));
    }
  };

  componentDidMount = () => {
    //getActiveCoin Crypto
    this.getCoinsCrypto();
    //getActiveCoin fiat after cryto success
    // this.getCoinsFiat();
    this.getWalletBalance();
    this.getPaymentMethods();
  };
  // get crypto converted banlce per unit

  getConvertedBalance = () => {
    let params = {
      from_coin: this.state.selectedCryptoName.toUpperCase(),
      to_coin: this.state.selectedFiatName.toUpperCase()
    };
    this.props
      .getCryptoConvertedPrice(params)
      .then(res => {
        this.setState({ calculatedValue: res, btcInUsd: res });
      })
      .catch(error => {});
  };

  getWalletBalance = () => {
    this.props
      .getWalletBalance()
      .then(res => {})
      .catch(error => {});
  };

  getPaymentMethods = () => {
    this.props
      .getUserPaymentMethods()
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {});
  };

  onSelectField = (e, data) => {
    console.log("DA", data);

    switch (data.label) {
      case "Identified people only": {
        this.setState({ identifiedPeopleCheck: data.checked });
        break;
      }
      case "Sms verification required": {
        this.setState({ smsVerificationCheck: data.checked });
        break;
      }
      case "Trusted people only": {
        this.setState({ TrustedPeopleCheck: data.checked });
        break;
      }
      case "Track liquidity": {
        this.setState({ liquidityCheck: data.checked });
        break;
      }
    }
  };

  getCoinsCrypto = () => {
    this.props
      .getCryptoActiveCoins()
      .then(res => {
        for (var i = 0; i < res.length; i++) {
          if (res[i].name === "btc") {
            this.props.dispatch(
              change("PostTradeForm", "coinId", res[i].coin_id)
            );

            this.setState(
              {
                defaultCrypto: res[i].coin_id,
                selectedCryptoName: res[i].name
              },
              () => {
                this.getCoinsFiat();
              }
            );
          }
        }
      })
      .catch(error => {});
  };
  getCoinsFiat = () => {
    this.props
      .getFiatActiveCoins()
      .then(res => {
        for (var i = 0; i < res.length; i++) {
          if (res[i].name === "usd") {
            this.props.dispatch(
              change("PostTradeForm", "currencyId", res[i].currency_id)
            );

            this.setState(
              {
                defaultFiat: res[i].currency_id,
                selectedFiatName: res[i].name
              },
              () => {
                this.getConvertedBalance();
              }
            );
          }
        }
      })
      .catch(error => {});
  };

  handleChangeOption = type => {
    if (type === "buy") {
      this.setState({ isSell: false });
    } else {
      this.setState({ isSell: true });
    }
  };

  finalStepForCreateOffer = values => {
    console.log("Final value before", values);

    var OpeningHours = [];
    var sunday = {};
    var monday = {};
    var tuesday = {};
    var wednesday = {};
    var thrusday = {};
    var friday = {};
    var saturday = {};

    //sunday
    if (values.hasOwnProperty("SunStart")) {
      sunday.day = "sunday";

      sunday.start = values.SunStart;
    }

    if (values.hasOwnProperty("SunEnd")) {
      sunday.day = "sunday";

      sunday.end = values.SunEnd;
    }

    //monday
    if (values.hasOwnProperty("MonStart")) {
      monday.day = "monday";

      monday.start = values.MonStart;
    }

    if (values.hasOwnProperty("MonEnd")) {
      monday.day = "monday";

      monday.end = values.MonEnd;
    }
    //tues
    if (values.hasOwnProperty("TueStart")) {
      tuesday.day = "tuesday";

      tuesday.start = values.TueStart;
    }

    if (values.hasOwnProperty("TueEnd")) {
      tuesday.day = "tuesday";

      tuesday.end = values.TueEnd;
    }
    //wed
    if (values.hasOwnProperty("WedStart")) {
      wednesday.day = "wednesday";

      wednesday.start = values.WedStart;
    }

    if (values.hasOwnProperty("WedEnd")) {
      wednesday.day = "wednesday";

      wednesday.end = values.WedEnd;
    }
    //thrus
    if (values.hasOwnProperty("ThuStart")) {
      thrusday.day = "thrusday";

      thrusday.start = values.ThuStart;
    }

    if (values.hasOwnProperty("ThuEnd")) {
      thrusday.day = "thrusday";

      thrusday.end = values.ThuEnd;
    }
    //friday
    if (values.hasOwnProperty("FriStart")) {
      friday.day = "friday";

      friday.start = values.FriStart;
    }

    if (values.hasOwnProperty("FriEnd")) {
      friday.day = "friday";

      friday.end = values.FriEnd;
    }
    //sat
    if (values.hasOwnProperty("SatStart")) {
      saturday.day = "saturday";

      saturday.start = values.SatStart;
    }

    if (values.hasOwnProperty("SatEnd")) {
      saturday.day = "saturday";

      saturday.end = values.SatEnd;
    }

    OpeningHours.push(monday);
    OpeningHours.push(tuesday);
    OpeningHours.push(wednesday);
    OpeningHours.push(thrusday);
    OpeningHours.push(friday);
    OpeningHours.push(saturday);
    OpeningHours.push(sunday);

    console.log("OPENING", OpeningHours);

    if (this.state.isSell) {
      var minTrans =
        parseFloat(values["minTrans"]) + parseFloat(this.state.addedMargin);

      var maxTrans =
        parseFloat(values["maxTrans"]) + parseFloat(this.state.addedMargin);
    } else {
      // in case of buy//
      var minTrans =
        parseFloat(values["minTrans"]) - parseFloat(this.state.addedMargin);

      var maxTrans =
        parseFloat(values["maxTrans"]) - parseFloat(this.state.addedMargin);
    }

    // new

    let finalData = {
      coinId: values["coinId"],
      currencyId: values["currencyId"],
      location: values["location"],
      paymentId: values["paymentId"],
      equation: values["equation"],
      maxTrans: maxTrans,
      quantity: values["quantity"],
      margin: values["margin"],
      minTrans: minTrans,
      terms: values["terms"],
      opening_hours: !this.state.isHoursSelected ? [] : OpeningHours,
      isTrackLiquidity: this.state.liquidityCheck,
      isSmsVerification: this.state.smsVerificationCheck,
      isTrustedOnly: this.state.TrustedPeopleCheck,
      isIdentifiedPeople: this.state.identifiedPeopleCheck,
      type: this.state.isSell ? "1" : "0",
      amount: this.state.btcInUsd
    };

    //new end

    this.props
      .createOffer(finalData)
      .then(res => {
        this.props.history.push(`${rootName}/auth/myOffer`);
      })
      .catch(error => {});
  };
  onChangeMargin = e => {
    let marginCalulated = (this.state.btcInUsd * e.target.value) / 100;
    this.setState({ addedMargin: marginCalulated.toFixed(4) });
  };
  sumbitForm = values => {
    //check if user have enough CRYPTO to sell  wntered in quantity field in case of Sell only.
    if (this.state.isSell) {
      for (var i = 0; i < this.props.walletBalance.length; i++) {
        if (
          this.props.walletBalance[i].name === this.state.selectedCryptoName
        ) {
          if (
            values["quantity"] <=
            smallestunitFormat(this.props.walletBalance[i].balance)
          ) {
            this.finalStepForCreateOffer(values);
          } else {
            console.log(
              "USER CURRENT BALANCE IN Selected crypto",
              smallestunitFormat(this.props.walletBalance[i].balance)
            );
            console.log("CALCULATED", this.state.calculatedValue);
            toast.error("Not sufficient balance");
          }
        }
      }
    } else {
      // In case of buy
      this.finalStepForCreateOffer(values);
    }
  };
  isOpeneningSelected = () => {
    this.setState({ isHoursSelected: true });
  };
  render() {
    console.log("FITAT", this.state);
    let { handleSubmit } = this.props;
    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="Buy Bitcoin" link="Back" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>

        <Container className="TradeBoxOut">
          <div className="TradeBox">
            <div className="TradeBoxHead">
              <h3>Advertisement rules and requirements</h3>
            </div>
            <div className="TradeBoxInfo">
              <List className="Advertisement">
                <List.Item>
                  For your ads to display you need to have Bitcoins in your
                  AnExchange wallet.
                </List.Item>
                {/* <List.Item>
                  Certain payment methods require you to be ID verified before
                  your ads are visible.
                </List.Item> */}
                {/* <List.Item>
                  Each completed trade costs advertisers 1% of the total trade
                  amount. See all fees on our fees page.
                </List.Item> */}
                <List.Item>
                  Once a trade is opened the price is final, except when there
                  is a clear mistake in the pricing.
                </List.Item>
                <List.Item>
                  You are not allowed to buy or sell Bitcoin on behalf of
                  someone else (brokering).
                </List.Item>
                <List.Item>
                  You may only use payment accounts that are registered in your
                  own name (no third party payments!).
                </List.Item>
                <List.Item>
                  You must provide your payment details in the advertisement or
                  in the trade chat.
                </List.Item>
                <List.Item>
                  All communication must happen on AnExchange.
                </List.Item>
                <List.Item>
                  {" "}
                  Payment methods marked High Risk have a significant risk of
                  fraud. Be careful and always ID verify your trading partners
                  when using high risk payment methods.
                </List.Item>
              </List>
            </div>
          </div>
          <Form onSubmit={handleSubmit(this.sumbitForm)}>
            <div className="TradeBox">
              <div className="TradeBoxHead">
                <h3>Trade type</h3>
              </div>
              <div className="TradeBoxInfo">
                <h2>
                  I want to...
                  <span>
                    {" "}
                    What kind of trade advertisement do you wish to create? If
                    you wish to sell bitcoins make sure you have bitcoins in
                    your AnExchange wallet.
                  </span>
                </h2>
                <div className="TradeInput">
                  <Form.Field className="TradeRadio">
                    <Form.Field>
                      <Radio
                        label="Sell online"
                        name="radioGroup"
                        checked={this.state.isSell}
                        onChange={() => this.handleChangeOption("sell")}
                      />
                    </Form.Field>
                    <Form.Field>
                      <Radio
                        label="Buy online"
                        name="radioGroup"
                        checked={!this.state.isSell}
                        onChange={() => this.handleChangeOption("buy")}
                      />
                    </Form.Field>
                  </Form.Field>
                </div>
              </div>
              <div className="TradeBox">
                <div className="TradeBoxInfo">
                  <div className="informationSec">
                    <Form.Group>
                      {this.state.defaultCrypto && (
                        <Form.Field>
                          <label>Currency</label>
                          <Field
                            name="coinId"
                            validate={[required()]}
                            component={SelectField}
                            defaultValue={this.state.defaultCrypto}
                            children={this.props.cryptoCoins}
                            placeholder="Currency"
                            selectOnBlur={false}
                          ></Field>
                        </Form.Field>
                      )}
                      <Form.Field>
                        <label>Location </label>
                        <Field
                          name="location"
                          type="text"
                          component={PlaceApi}
                          placeholder="Location"
                          validate={[required()]}
                        />
                        <span>
                          What kind of trade advertisement do you wish to
                          create? If you wish to sell bitcoins make sure you
                          have bitcoins in your AnExchange wallet.
                        </span>
                      </Form.Field>
                    </Form.Group>
                  </div>
                </div>
              </div>
            </div>

            <div className="TradeBox">
              <div className="TradeBoxHead">
                <h3>More information</h3>
              </div>
              <div className="TradeBoxInfo">
                <div className="informationSec">
                  <Form.Group>
                    {this.state.defaultFiat && (
                      <Form.Field>
                        <label>Currency</label>
                        <Field
                          name="currencyId"
                          validate={[required()]}
                          component={SelectField}
                          defaultValue={this.state.defaultFiat}
                          children={this.props.fiatCoins}
                          placeholder="Currency"
                          selectOnBlur={false}
                        ></Field>
                      </Form.Field>
                    )}
                    <Form.Field>
                      <label>Payment method</label>
                      <Field
                        name="paymentId"
                        validate={[required()]}
                        component={SelectField}
                        children={this.props.paymentMethods}
                        placeholder="Payment method"
                        selectOnBlur={false}
                      ></Field>
                      <span>
                        Required. The name or identifying code of the bank you
                        are using.{" "}
                      </span>
                    </Form.Field>
                  </Form.Group>
                  <Form.Group>
                    <Form.Field>
                      <label>Quantity</label>
                      <Field
                        name="quantity"
                        type="number"
                        component={FormField}
                        placeholder="0"
                        normalize={amountCheckCrypto}
                        onChange={e => this.quantityChanged(e)}
                        validate={[required(), numericality({ ">": 0 })]}
                      />
                    </Form.Field>
                    <Form.Field>
                      <label>
                        Price equation{" "}
                        <span className="spaneq">
                          {this.state.btcInUsd}{" "}
                          {this.state.selectedFiatName.toUpperCase()} / BTC
                        </span>
                      </label>
                      <Field
                        name="equation"
                        type="text"
                        component={FormField}
                        placeholder={`btc_in_${this.state.selectedFiatName} * quantity`}
                        readOnly
                      />
                      <span>
                        How the trade price is determined from the hourly market
                        price. For more information about equations how to
                        determine your trading price see pricing FAQ. Please
                        note that the advertiser is always responsible for all
                        payment processing fees.{" "}
                      </span>
                    </Form.Field>
                  </Form.Group>
                  <Form.Group>
                    <Form.Field>
                      <label>
                        Margin(%)
                        <span>{this.state.selectedFiatName.toUpperCase()}</span>
                      </label>
                      <Field
                        name="margin"
                        type="number"
                        component={FormField}
                        placeholder="0"
                        // normalize={marginLimitCheck}
                        validate={[
                          required(),
                          numericality({ int: true, ">": 0, "<": 100 })
                        ]}
                        onChange={e => this.onChangeMargin(e)}
                      />
                      <span>
                        Margin percentage will auto add while selling and
                        subtract in case of buying
                      </span>
                    </Form.Field>
                  </Form.Group>
                  <Form.Group>
                    <Form.Field>
                      <label>
                        Min. transaction limit{" "}
                        <span>{this.state.selectedFiatName.toUpperCase()}</span>
                      </label>
                      <Field
                        name="minTrans"
                        type="number"
                        normalize={amountCheckUsd}
                        component={FormField}
                        placeholder="0"
                        validate={[required(), numericality({ ">": 0 })]}
                      />
                    </Form.Field>
                    <Form.Field>
                      <label>
                        Max. transaction limit{" "}
                        <span>{this.state.selectedFiatName.toUpperCase()}</span>
                      </label>

                      <Field
                        name="maxTrans"
                        type="number"
                        normalize={amountCheckUsd}
                        component={FormField}
                        placeholder="0"
                        readOnly
                        validate={[required()]}
                      />
                      <span>
                        Optional. Maximum transaction limit in one trade. For
                        online sells, your AnExchange.com wallet balance may
                        limit the maximum fundable trade also{" "}
                      </span>
                    </Form.Field>
                  </Form.Group>

                  <Grid divided="vertically" stackable>
                    <span className="openHours">
                      <b>Opening hours</b> (Optional. Days and hours when you
                      want your advertisement to be automatically shown and
                      hidden.)
                    </span>
                    <Grid.Row columns={2} className="dayTimeList">
                      <Grid.Column>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Sun</label>
                            {/* <Select placeholder="Start" options={startTime} /> */}
                            <Field
                              name="SunStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="SunEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Mon</label>
                            <Field
                              name="MonStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="MonEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Tue</label>
                            <Field
                              name="TueStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="TueEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Wed</label>
                            <Field
                              name="WedStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="WedEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                      </Grid.Column>
                      <Grid.Column>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Thu</label>
                            <Field
                              name="ThuStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="ThuEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Fri</label>
                            <Field
                              name="FriStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="FriEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                        <Form.Group className="dayTime">
                          <Form.Field>
                            <label>Sat</label>
                            <Field
                              name="SatStart"
                              component={SelectField}
                              children={startTime}
                              placeholder="start"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                            <Field
                              name="SatEnd"
                              component={SelectField}
                              children={startTime}
                              placeholder="End"
                              selectOnBlur={false}
                              onChangeText={() => this.isOpeneningSelected()}
                            ></Field>
                          </Form.Field>
                        </Form.Group>
                      </Grid.Column>
                    </Grid.Row>
                  </Grid>

                  <Form.Field>
                    <label>Terms of trade </label>
                    {/* <TextArea placeholder="0" /> */}
                    <Field
                      name="terms"
                      component={TextAreas}
                      placeholder="Terms and conditions"
                      validate={[required()]}
                    />
                    <span>
                      Other information you wish to tell about your trade.
                      Example 1: This advertisement is only for cash trades. If
                      you want to pay online, contact AnExchange.com/ad/1234.
                      Example 2: Please make request only when you can complete
                      the payment with cash within 12 hours.{" "}
                    </span>
                  </Form.Field>
                </div>
              </div>
            </div>

            <div className="TradeBox">
              <div className="TradeBoxHead">
                <h3>Liquidity options</h3>
              </div>
              <div className="TradeBoxInfo">
                <List className="CheckList">
                  <List.Item>
                    {" "}
                    <Checkbox
                      label="Track liquidity"
                      checked={this.state.liquidityCheck}
                      onChange={(e, data) => this.onSelectField(e, data)}
                    />
                    <span>
                      This option limits the liquidity of this advertisement to
                      the max. transaction limit. Buyers cannot open and
                      complete trades for more than this amount.
                      <br />
                    </span>
                    <span>
                      Example: With track liquidity turned on and max.
                      transaction limit set to 100 USD when a buyer opens a
                      trade for 20 USD the max. transaction limit is
                      automatically decreased to 80 USD. It returns to 100 USD
                      if the buyer cancels the trade, and stays at 80 USD if the
                      trade is completed.
                    </span>
                  </List.Item>
                </List>
              </div>
            </div>
            <div className="TradeBox">
              <div className="TradeBoxHead">
                <h3>Security options</h3>
              </div>
              <div className="TradeBoxInfo">
                <List className="CheckList">
                  <List.Item>
                    {" "}
                    <Checkbox
                      label="Identified people only"
                      checked={this.state.identifiedPeopleCheck}
                      onChange={(e, data) => this.onSelectField(e, data)}
                    />
                    <span>
                      To contact your advertisement, users need to verify their
                      identity by sending IDs, driver's licence or passport.{" "}
                    </span>
                  </List.Item>
                  <List.Item>
                    {" "}
                    <Checkbox
                      label="Sms verification required"
                      checked={this.state.smsVerificationCheck}
                      onChange={(e, data) => this.onSelectField(e, data)}
                    />
                    <span>
                      Only contacts with a verified mobile phone number can
                      contact you from the advertisement{" "}
                    </span>
                  </List.Item>
                  <List.Item>
                    {" "}
                    <Checkbox
                      label="Trusted people only"
                      checked={this.state.TrustedPeopleCheck}
                      onChange={(e, data) => this.onSelectField(e, data)}
                    />
                    <span>
                      Restrict your advertisement to be shown only to users that
                      you have marked as Trusted.{" "}
                      <a href>Learn how to mark users as trusted.</a>
                    </span>
                  </List.Item>
                </List>
              </div>
            </div>
            <Button type="submit">Publish advertisement</Button>
          </Form>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    fiatCoins: state.currency.fiatCoins,
    cryptoCoins: state.currency.cryptoCoins,
    paymentMethods: state.currency.paymentMethods,
    walletBalance: state.currency.walletBalance
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getCryptoActiveCoins: () => dispatch(getCryptoActiveCoins()),
    getFiatActiveCoins: () => dispatch(getFiatActiveCoins()),
    getUserPaymentMethods: () => dispatch(getUserPaymentMethods()),
    getWalletBalance: () => dispatch(getWalletBalance()),
    createOffer: data => dispatch(createOffer(data)),
    getCryptoConvertedPrice: data => dispatch(getCryptoConvertedPrice(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "PostTradeForm" })(PostTrade));
