import { UserService } from "../../services/UserService";
import { CoinService } from "../../services/CoinServices";
import { TOKEN } from "../../config/Constants";
import { startStopLoading } from "./loading";
import { toast } from "../../Components/Toast/Toast";

const GET_CRYPTO_COINS = "GET_CRYPTO_COINS";
const GET_FIAT_COINS = "GET_FIAT_COINS";
const GET_PAYEMENT_METHODS = "GET_PAYEMENT_METHODS";
const OPEN_CLOSE_EDIT_METHOD = "OPEN_CLOSE_EDIT_METHOD";
const GET_WALLET_BALANCE = "GET_WALLET_BALANCE";
const GET_CRYPTO_CONVERTED_PRICE = "GET_CRYPTO_CONVERTED_PRICE";

const initialState = {
  jwtToken: undefined,
  cryptoCoins: [],
  fiatCoins: [],
  paymentMethods: [],
  openEditPayment: false,
  editPaymentData: {},
  walletBalance: [],
  cryptoConvertedPrice: ""
};

export default function currency(state = initialState, action = {}) {
  switch (action.type) {
    case GET_CRYPTO_COINS:
      return { ...state, cryptoCoins: action.result };
    case GET_FIAT_COINS:
      return { ...state, fiatCoins: action.result };
    case GET_PAYEMENT_METHODS:
      return { ...state, paymentMethods: action.result };
    case OPEN_CLOSE_EDIT_METHOD:
      return {
        ...state,
        openEditPayment: action.result,
        editPaymentData: action.data
      };
    case GET_WALLET_BALANCE:
      return {
        ...state,
        walletBalance: action.result
      };
    case GET_CRYPTO_CONVERTED_PRICE:
      return {
        ...state,
        cryptoConvertedPrice: action.result
      };
    default:
      return state;
  }
}
export const getCryptoActiveCoins = data => {
  return (dispatch, getState) => {
    dispatch(startStopLoading(true));

    return new Promise((resolve, reject) => {
      CoinService.getCryptoCoins({
        jwt: localStorage.getItem("JwtToken")
      })
        .then(res => {
          //   dispatch(startStopLoading(false));

          console.log("corona", res.data.data);
          var currency = res.data.data;
          for (var i = 0; i < currency.length; i++) {
            currency[i].value = currency[i].coin_id;
            currency[i].text = currency[i].name.toUpperCase();
          }
          dispatch({ type: GET_CRYPTO_COINS, result: currency });
          resolve(res.data.data);
        })
        .catch(ex => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};
