export const getUserPaymentMethods = data => {
  return (dispatch, getState) => {
    dispatch(startStopLoading(true));

    return new Promise((resolve, reject) => {
      CoinService.getPaymentMethods({
        jwt: localStorage.getItem("JwtToken")
      })
        .then(res => {
          dispatch(startStopLoading(false));

          let methods = res.data.data;
          for (var i = 0; i < methods.length; i++) {
            methods[i].value = methods[i].paymentId;
            methods[i].text = methods[i].payment_type;
          }
          dispatch({ type: GET_PAYEMENT_METHODS, result: methods });
          resolve(res);
        })
        .catch(ex => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};

export const openCloseEditModal = (status, editData) => {
  return (dispatch, getState) => {
    dispatch({ type: OPEN_CLOSE_EDIT_METHOD, result: status, data: editData });
  };
};

export const addPaymentMethods = data => {
  return (dispatch, getState) => {
    dispatch(startStopLoading(true));

    return new Promise((resolve, reject) => {
      CoinService.addPaymentMethod(data, {
        jwt: localStorage.getItem("JwtToken")
      })
        .then(res => {
          toast.success(res.data.message);

          dispatch(startStopLoading(false));

          resolve(res);
        })
        .catch(ex => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};

