import React, { Component } from "react";
import "./AddPayment.scss";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import {
  Container,
  Breadcrumb,
  Table,
  Icon,
  Form,
  Button,
  Confirm
} from "semantic-ui-react";
import { Field, reduxForm, reset } from "redux-form";
import { required, /*email*/ } from "redux-form-validators";
import { connect } from "react-redux";
import {
  FormField,
  SelectField,
 // InputSelectWrap
} from "../../../Components/Common/index";
import {
  getUserPaymentMethods,
  openCloseEditModal,
  addPaymentMethods,
  removePaymentMethods
} from "../../../redux/reducers/currency";
import EditPayment from "./EditPayment";

const sections = [
  { key: "edit", content: "Dashboard", link: true },
  { key: "Shirt", content: "Add Payment ", active: true }
];
export class AddPayment extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
      deleteMethodId: ""
    };
  }

  show = edit => {
    // this.props.reset("AddPaymentForm"); // requires form name
    this.props.openCloseEditModal(true, edit);
  };
  componentDidMount = () => {
    this.props.openCloseEditModal(false, {});
    this.getPaymentMethods();
  };
  openConfirm = data => {
    this.setState({ open: true, deleteMethodId: data.paymentId });
  };
  closeConfirm = () => {
    this.setState({ open: false });
  };
  deletePaymentMethod = () => {
    let data = {
      paymentId: this.state.deleteMethodId
    };
    this.props
      .removePaymentMethods(data)
      .then(res => {
        this.setState({ open: false, deleteMethodId: "" });
        this.getPaymentMethods();
      })
      .catch(error => {});
  };
  sumbitForm = values => {
    this.props
      .addPaymentMethods(values)
      .then(res => {
        this.props.dispatch(reset("AddPaymentForm")); // requires form name

        this.getPaymentMethods();
      })
      .catch(error => {});
  };
  sumbitEditForm = () => {
    alert("edit");
  };

  getPaymentMethods = () => {
    this.props
      .getUserPaymentMethods()
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {});
  };
  render() {
    const { open, size } = this.state;
    let { handleSubmit } = this.props;
    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="Add Payment" />
          </Container>
          <Container fluid className="TopBreadcrumb">
            <Container>
              <Breadcrumb icon="right angle" sections={sections} />
            </Container>
          </Container>
        </Container>
        <Container className="AddPaymentBox">
          <div className="AddPaymentForm">
            <div className="AddPaymentFormHead">
              <h3>Add Payment</h3>
            </div>
            <div className="AddPaymentInfo">
              <Form
                className="informationSec"
                onSubmit={handleSubmit(this.sumbitForm)}
              >
                <Form.Group>
                  <Form.Field>
                    <label>Payment method</label>
                    <Field
                      type="text"
                      name="payment_type"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Payment method"
                    ></Field>
                  </Form.Field>
                  <Form.Field>
                    <label>Currency</label>

                    <Field
                      name="currency_id"
                      selectOnBlur={false}
                      validate={[required()]}
                      component={SelectField}
                      children={this.props.fiatCoins}
                      placeholder="Select your currency"
                    ></Field>
                  </Form.Field>
                </Form.Group>
                <Form.Group>
                  <Form.Field>
                    <label>Identifier</label>
                    <Field
                      type="text"
                      name="payment_identifier"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Identifier"
                    ></Field>
                  </Form.Field>
                  <Form.Field>
                    <label>Description</label>
                    <Field
                      type="text"
                      name="detailed_info"
                      validate={[required()]}
                      component={FormField}
                      placeholder="Description"
                    ></Field>
                  </Form.Field>
                </Form.Group>
                <Form.Field className="addBtn">
                  <Button type="submit">Add</Button>
                </Form.Field>
              </Form>
            </div>
          </div>

          <div className="paymentList">
            <Table inverted unstackable>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell>Payment method</Table.HeaderCell>
                  <Table.HeaderCell>Currency</Table.HeaderCell>
                  <Table.HeaderCell>Identifier</Table.HeaderCell>
                  <Table.HeaderCell>Action</Table.HeaderCell>
                </Table.Row>
              </Table.Header>
              <Table.Body>
                {this.props.paymentMethods.map((data, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{data.payment_type}</Table.Cell>
                    <Table.Cell>Currency</Table.Cell>
                    <Table.Cell>{data.payment_identifier} </Table.Cell>
                    <Table.Cell>
                      <a href className="btn" onClick={() => this.show(data)}>
                        {" "}
                        <Icon name="edit" />
                      </a>{" "}
                      <a href className="btn" onClick={() => this.openConfirm(data)}>
                        <Icon name="delete" />
                      </a>
                    </Table.Cell>
                  </Table.Row>
                ))}
              </Table.Body>
            </Table>
          </div>
        </Container>
        <Confirm
          open={this.state.open}
          onCancel={this.closeConfirm}
          onConfirm={this.deletePaymentMethod}
        />
        {this.props.openEditPayment && <EditPayment></EditPayment>}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    fiatCoins: state.currency.fiatCoins,
    cryptoCoins: state.currency.cryptoCoins,
    paymentMethods: state.currency.paymentMethods,
    openEditPayment: state.currency.openEditPayment
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getUserPaymentMethods: () => dispatch(getUserPaymentMethods()),
    openCloseEditModal: (status, data) =>
      dispatch(openCloseEditModal(status, data)),
    addPaymentMethods: data => dispatch(addPaymentMethods(data)),
    removePaymentMethods: data => dispatch(removePaymentMethods(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "AddPaymentForm" })(AddPayment));
