import { SMALLEST_UNIT, TOTAL_LIMIT } from "../config/Constants";

export function smallestunitFormat(value, limit) {
  let args = 0;
  if (value > 0 && value < 1) {
    args = 8;
  }
  if (value <= 0) {
    value = "0.0";
  } else {
    let firstBlncTest = String(value).split(".", 2);
    if (firstBlncTest.length > 1) {
      if (firstBlncTest[1].length > args) {
        let presc3 = firstBlncTest[1].substring(0, args);
        value = firstBlncTest[0] + "." + presc3;
        if (value <= 0) value = "0.0";
      }
    }
  }
  if (limit) {
    value = value / TOTAL_LIMIT;
  } else {
    value = value / SMALLEST_UNIT;
  }
  value = convertExponentialToDecimal(value);
  return value;
}
export function convertExponentialToDecimal(exponentialNumber) {
  const str = exponentialNumber.toString();
  if (str.indexOf("e") !== -1) {
    // const exponent = parseInt(str.split("-")[1], 18);
    const result = exponentialNumber.toFixed(20);
    return result;
  } else {
    return exponentialNumber;
  }
}
export const listDateFormat = date => {
  let newDAte = new Date(date);
  let parsedDate = newDAte.toLocaleDateString("default", {
    year: "numeric",
    month: "short",
    day: "numeric"
  });
  let time = newDAte.toLocaleTimeString("default", {
    hour12: false,
    hour: "2-digit",
    minute: "2-digit"
  });
  return parsedDate + " " + "| " + time;
};

export const amountCheckCrypto = (value, previousValue, allValues) => {
  if (value.length === 1 && value[0] === ".") {
    value = "0.";
  }
  value.replace(/[^0-9\.]/g, "");
  if (!(value.indexOf(".") == -1)) {
    if ((value.toString().split(".")[1].length || 0) > 8) {
      return previousValue;
    } else {
      return value;
    }
  }
  return value;
};

export const amountCheckUsd = (value, previousValue, allValues) => {
  if (value.length === 1 && value[0] === ".") {
    value = "0.";
  }
  value.replace(/[^0-9\.]/g, "");
  if (!(value.indexOf(".") == -1)) {
    if ((value.toString().split(".")[1].length || 0) > 3) {
      return previousValue;
    } else {
      return value;
    }
  }
  return value;
};

export const deleteCookie = (c_name, value, exdays) => {
  var exdate = new Date();
  exdate.setDate(exdate.getDate() - exdays);
  var c_value =
    escape(value) + (exdays == null ? "" : "; expires=" + exdate.toUTCString());
  document.cookie = c_name + "=" + c_value;
};
