const BuySellHistory = ({ type, record, buySellOffer }) => {
  console.log("data", record);

  return (
    <Tab.Pane attached={false}>
      <Table inverted selectable>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>
              {type === "BUY" ? "Seller" : "Buyer"}
            </Table.HeaderCell>
            <Table.HeaderCell>Payment method</Table.HeaderCell>
            <Table.HeaderCell>Price/BTC</Table.HeaderCell>
            <Table.HeaderCell>Limits</Table.HeaderCell>
            <Table.HeaderCell>Action</Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {record.map((data, index) => (
            <Table.Row key={index}>
              <Table.Cell>
                <a href>{data.email}</a>{" "}
              </Table.Cell>
              <Table.Cell>{data.payment_type}</Table.Cell>
              <Table.Cell>
                <a href>
                  {smallestunitFormat(data.amount)}{" "}
                  {data.fiat_coin.toUpperCase()}
                </a>{" "}
              </Table.Cell>
              <Table.Cell>
                {smallestunitFormat(data.min_transaction)} -{" "}
                {smallestunitFormat(data.max_transaction)}{" "}
                {data.fiat_coin.toUpperCase()}{" "}
              </Table.Cell>
              <Table.Cell>
                <a href className="btn" onClick={() => buySellOffer(data, type)}>
                  {type}
                </a>
              </Table.Cell>
            </Table.Row>
          ))}
          {record.length === 0 && (
            <Table.Row>
              <Table.Cell style={{ textAlign: "center" }} ColSpan="5">
                No records found
              </Table.Cell>
            </Table.Row>
          )}
        </Table.Body>
      </Table>
    </Tab.Pane>
  );
};

const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Shirt", content: "Sell Bitcoin", active: true }
];

class Dashboard extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      defaultTab: 0,
      defaultTabList: 0,
      listType: 1,
      buySellPage: 1,
      blockedId: "",
      bidType: ""
    };
  }

  buySellOffer = (data, type) => {
    this.props.startStopLoading(true);
    let offer = {
      offer: data
    };
    this.setState({ blockedId: data.o_id, bidType: type }, () => {
      this.props.startStopLoading(false);

      if (this.state.bidType === "BUY") {
        // selected buy offer in persist
        this.props.saveOfferDetail(offer);

        this.props.history.push(`${rootName}/auth/BuyBitcoin`, { id: 1 });
      } else {
        //selected sell offer in persist
        this.props.saveOfferDetailSell(offer);
        this.props.history.push(`${rootName}/auth/SellBitcoin`);
      }
    });
  };
  emptyPersist = async () => {
    //empty buy persist on each time when landing to this component
    this.props.saveBuyOfferDetail({});
    // empty selected offer details
    this.props.saveOfferDetail({});

    //empty sell persisit
    this.props.saveSellOfferDetail({});
    // empty selected offer details
    this.props.saveOfferDetailSell({});
  };

  componentDidMount = () => {
    this.emptyPersist();
    // get buysell list
    this.getBuySellList();
    //getActiveCoin Crypto
    this.getCoinsCrypto();

    //getActiveCoin fiat
    this.getCoinsFiat();

    //socket block listner
    socket.on("block", res => {
      this.props.startStopLoading(false);

      this.getBuySellList();

      console.log("block LISTEN", res);
    });
    // socket unblock listner
    socket.on("unblocked", res => {
      this.getBuySellList();
      console.log("unblock LISTEN", res);
    });
    // new offer created
    socket.on("newoffer", res => {
      this.getBuySellList();
      console.log("New Offer LISTEN", res);
    });
  };

  getCoinsCrypto = () => {
    this.props
      .getCryptoActiveCoins()
      .then(res => {})
      .catch(error => {});
  };
  getCoinsFiat = () => {
    this.props
      .getFiatActiveCoins()
      .then(res => {})
      .catch(error => {});
  };

  getBuySellList = () => {
    var d = new Date();
    var day = d.getDay();
    var min = d.getMinutes();
    var hour = d.getHours();
    let data = {
      type: this.state.listType,
      page: this.state.buySellPage,
      pagination: 1,
      self: 0,
      time: hour + ":" + min,
      filter: 1,
      day:
        day === 0
          ? "sunday"
          : day === 1
          ? "monday"
          : day === 2
          ? "tuesday"
          : day === 3
          ? "wednesday"
          : day === 4
          ? "thrusday"
          : day === 5
          ? "friday"
          : "saturday"
    };
    this.props
      .getBuySellOffer(data)
      .then(res => {
        console.log("RES", res);
      })
      .catch(error => {
        console.log(error);
      });
  };

  tabChange = (e, data) => {
    //reset form
    this.props.reset("OfferForm"); // requires form name
    this.setState(
      {
        defaultTab: data.activeIndex
      },
      () => {
        // getBuySellList
        this.getBuySellList();
      }
    );
  };
  listTabChange = (e, data) => {
    this.setState(
      {
        defaultTabList: data.activeIndex,
        listType: data.activeIndex === 0 ? 1 : 0,
        buySellPage: 1
      },
      () => {
        // getBuySellList
        this.getBuySellList();
      }
    );
  };
  pageChange = (e, data) => {
    console.log(data.activePage);
    this.setState({ buySellPage: data.activePage }, () => {
      this.getBuySellList();
    });
  };
  render() {
    const panes = [
      {
        menuItem: "Quick Buy",
        render: () => (
          <BuyTemlates
            type="buy"
            handleSubmit={this.props.handleSubmit}
            onSubmit={this.props.handleSubmit}
          />
        )
      },
      {
        menuItem: "Quick Sell",
        render: () => (
          <SellTemlates
            handleSubmit={this.props.handleSubmit}
            onSubmit={this.props.handleSubmit}
            type="sell"
          />
        )
      }
    ];

    //listing buysell
    const historyBuySell = [
      {
        menuItem: "Buy Crypto coins",
        render: () => (
          <BuySellHistory
            type="BUY"
            record={this.props.buySellList}
            buySellOffer={this.buySellOffer}
          />
        )
      },
      {
        menuItem: "Sell Crypto coins",
        render: () => (
          <BuySellHistory
            type="SELL"
            record={this.props.buySellList}
            buySellOffer={this.buySellOffer}
          />
        )
      }
    ];

    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle title="Buy Bitcoin" />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>
        <Container className="MainContainer">
          <div className="tabsBox">
            <Tab
              menu={{ secondary: true }}
              panes={panes}
              onTabChange={(e, data) => this.tabChange(e, data)}
              activeIndex={this.state.defaultTab}
            />
          </div>

          <div className="tabsBox tabsBoxSec">
            {/* <a href className="refreshBtn">Refresh List</a> */}
            <Tab
              menu={{ secondary: true }}
              panes={historyBuySell}
              activeIndex={this.state.defaultTabList}
              onTabChange={(e, data) => this.listTabChange(e, data)}
            />
          </div>
          {this.props.totalOffer > 0 && (
            <div className="pageBottom">
              <Pagination
                boundaryRange={0}
                activePage={this.state.buySellPage}
                ellipsisItem={null}
                firstItem={null}
                lastItem={null}
                siblingRange={1}
                totalPages={Math.ceil(this.props.totalOffer / 10)}
                onPageChange={(e, data) => this.pageChange(e, data)}
              />
            </div>
          )}
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX", state);
  return {
    selectedOfferDetail: state.persist.selectedOfferDetail,

    buySellList: state.offer.buySellOffer,
    totalOffer: state.offer.totalOffer
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getBuySellOffer: data => dispatch(getBuySellOffer(data)),
    getCryptoActiveCoins: () => dispatch(getCryptoActiveCoins()),
    getFiatActiveCoins: () => dispatch(getFiatActiveCoins()),
    checkAuthToken: token => dispatch(checkAuthToken(token)),
    startStopLoading: status => dispatch(startStopLoading(status)),
    saveOfferDetail: data => dispatch(saveOfferDetail(data)),
    saveBuyOfferDetail: data => dispatch(saveBuyOfferDetail(data)),

    saveOfferDetailSell: data => dispatch(saveOfferDetailSell(data)),
    saveSellOfferDetail: data => dispatch(saveSellOfferDetail(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferForm" })(Dashboard));
