const SELCTED_OFFER_DETAIL = "SELCTED_OFFER_DETAIL";
const SAVE_BUY_DETAILS = "SAVE_BUY_DETAILS";

const SELCTED_OFFER_DETAIL_SELL = "SELCTED_OFFER_DETAIL_SELL";
const SAVE_SELL_DETAILS = "SAVE_SELL_DETAILS";

const initialState = {
  selectedOfferDetail: {},
  saveBuyDetails: {},
  selectedOfferDetailSell: {},
  saveSellDetails: {}
};

export default function persist(state = initialState, action = {}) {
  switch (action.type) {
    case SELCTED_OFFER_DETAIL:
      return { ...state, selectedOfferDetail: action.result };
    case SAVE_BUY_DETAILS:
      return {
        ...state,
        saveBuyDetails: action.result
      };
    case SELCTED_OFFER_DETAIL_SELL:
      return { ...state, selectedOfferDetailSell: action.result };
    case SAVE_SELL_DETAILS:
      return {
        ...state,
        saveSellDetails: action.result
      };
    default:
      return state;
  }
}
//buy details
export const saveOfferDetail = data => {
  return (dispatch, getState) => {
    dispatch({ type: SELCTED_OFFER_DETAIL, result: data });
  };
};

export const saveBuyOfferDetail = data => {
  return (dispatch, getState) => {
    dispatch({ type: SAVE_BUY_DETAILS, result: data });
  };
};

//sell details
export const saveOfferDetailSell = data => {
  return (dispatch, getState) => {
    dispatch({ type: SELCTED_OFFER_DETAIL_SELL, result: data });
  };
};

export const saveSellOfferDetail = data => {
  return (dispatch, getState) => {
    dispatch({ type: SAVE_SELL_DETAILS, result: data });
  };
};
