import React, { Component } from "react";
import PageTitle from "../../../Components/PageTitle/PageTitle";
import {
  Container,
  Breadcrumb,
  Grid,
  List,
  Button,
  Form,
  Select
} from "semantic-ui-react";
import { Field, reduxForm, reset, change } from "redux-form";
import { required, email } from "redux-form-validators";
import { connect } from "react-redux";
import { FormField, SelectField } from "../../../Components/Common/index";
import { getUserPaymentMethods } from "../../../redux/reducers/currency";
import "./MakePaymentSell.scss";
import { buySellOffer } from "../../../redux/reducers/offer";
import { listDateFormat, smallestunitFormat } from "../../../Utils/helper";
import io from "socket.io-client";
import { socket } from "../../../App";

import { SOCKET_URL, rootName } from "../../../constant";
// let socket = io.connect(SOCKET_URL, {
//   reconnect: true,
//   transports: ["websocket"]
// });
const sections = [
  { key: "Home", content: "Dashboard", link: true },
  { key: "Sell", content: "Sell Bitcoin", link: true },
  { key: "confirm", content: "Sell Bitcoin", active: true }
];

export class MakePaymentSell extends Component {
  componentWillUnmount = () => {
    console.log(this.props.history);
    if (
      this.props.history.location.pathname === `${rootName}/auth/SellBitcoin`
    ) {
      //your code... on back button
      socket.emit("unblocked_single", {
        offer_id: this.props.selectedOfferDetailSell.offer.o_id
      });
    }
  };
  componentDidMount = () => {
    window.scrollTo(0, 0);

    // socket unblock listner
    socket.on("unblocked", res => {
      this.props.history.push(`${rootName}`);
    });
    if (Object.keys(this.props.saveSellDetails).length !== 0) {
      this.getUserPayementMethod();
    } else {
      this.props.history.push(`${rootName}/`);
    }
  };
  getUserPayementMethod = () => {
    this.props
      .getUserPaymentMethods()
      .then(res => {})
      .catch(error => {});
  };
  sumbitForm = values => {
    let { selectedOfferDetailSell, saveSellDetails } = this.props;
    var data = {
      offer_id: selectedOfferDetailSell.offer.o_id,
      bid_type: selectedOfferDetailSell.offer.type === 1 ? 0 : 1,
      quantity: saveSellDetails.quantity,
      amount: saveSellDetails.amount,
      other_info: saveSellDetails.other_info,
      payment_id: values["payment_id"]
    };

    this.props
      .buySellOffer(data)
      .then(res => {
        if (Object.keys(this.props.selectedOfferDetailSell).length !== 0) {
          socket.emit("unblocked_single", {
            offer_id: this.props.selectedOfferDetailSell.offer.o_id
          });
        }
        this.props.history.push(`${rootName}/auth/myBids`);
      })
      .catch(error => {});
  };
  render() {
    let { selectedOfferDetailSell, handleSubmit, saveSellDetails } = this.props;
    return (
      <div>
        <Container fluid>
          <Container className="TopHeader">
            <PageTitle
              title={`Sell  ${selectedOfferDetailSell.offer.crypto_coin.toUpperCase()}`}
              link="Back"
            />
          </Container>
        </Container>
        <Container fluid className="TopBreadcrumb">
          <Container>
            <Breadcrumb icon="right angle" sections={sections} />
          </Container>
        </Container>

        <Container>
          <div className="PaymentSec">
            <h2>Seller Matched Make Payment!</h2>
            <h3>
              Offer Price{" "}
              <span>
                {smallestunitFormat(selectedOfferDetailSell.offer.amount)}{" "}
                {selectedOfferDetailSell.offer.fiat_coin.toUpperCase()} of 1{" "}
                {selectedOfferDetailSell.offer.crypto_coin.toUpperCase()}
              </span>
            </h3>
            <h4>
              Placed on{" "}
              {listDateFormat(selectedOfferDetailSell.offer.created_at, 1)}
            </h4>
            <Form onSubmit={handleSubmit(this.sumbitForm)}>
              <div className="PaymentSecIn">
                <div className="paymentInfo">
                  <h4>Your payement information</h4>

                  <Form.Field>
                    <label>Credited to</label>
                    <Field
                      name="payment_id"
                      validate={[required()]}
                      component={SelectField}
                      children={this.props.paymentMethods}
                      placeholder="Payment method"
                      selectOnBlur={false}
                    ></Field>
                  </Form.Field>
                </div>
              </div>
              <Grid divided="vertically" className="PaymentSecBot">
                <Grid.Row columns={2}>
                  <Grid.Column className="PaymentSecLeft">
                    <p>Pay Exactly</p>
                    <p className="fund">
                      {this.props.saveSellDetails.amount}{" "}
                      {this.props.selectedOfferDetailSell.offer.fiat_coin.toUpperCase()}
                    </p>
                    <i>(Do not round off)</i>
                  </Grid.Column>
                  <Grid.Column className="PaymentSecRight">
                    <Button type="submit">Send Sell Trade Request</Button>
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            </Form>
          </div>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("SATTE REDUX selectedOfferDetailSell", state);
  return {
    selectedOfferDetailSell: state.persist.selectedOfferDetailSell,
    paymentMethods: state.currency.paymentMethods,
    saveSellDetails: state.persist.saveSellDetails
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getUserPaymentMethods: () => dispatch(getUserPaymentMethods()),
    buySellOffer: data => dispatch(buySellOffer(data))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(reduxForm({ form: "OfferBidFormStep2" })(MakePaymentSell));
