import { fetch } from "./Fetch";
import { API_HOST } from "../constant";

const getOfferBuySell = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/getoffer`, data, options);
};

const createOffer = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/offer`, data, options);
};
const cancelOffer = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/canceloffer`, data, options);
};
const buySellOffer = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/offerbid`, data, options);
};
const buySellBids = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/sentBids`, data, options);
};
const buySellBidsRecieved = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/receivedbids`, data, options);
};
const approveBuyBid = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/approvebuy`, data, options);
};
const disapproveBuyBid = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/disapproved`, data, options);
};
const sendMoney = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/sendmoney`, data, options);
};
const approvesentSell = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/approvesentSell`, data, options);
};
const getUserDetails = (data, options) => {
  return fetch("post", `${API_HOST}users/user/getuserdetail`, data, options);
};
export const OfferService = {
  getOfferBuySell,
  createOffer,
  cancelOffer,
  buySellOffer,
  buySellBids,
  buySellBidsRecieved,
  approveBuyBid,
  disapproveBuyBid,
  sendMoney,
  approvesentSell,
  getUserDetails
};
