import { fetch } from "./Fetch";
import { API_HOST } from "../constant";

const getCryptoCoins = (options) => {
  return fetch("get", `${API_HOST}landing/ptp/ptpcoins`, {}, options);
};
const getFiatCoins = (options) => {
  return fetch("get", `${API_HOST}landing/ptp/ptpcurrencies`, {}, options);
};
const getPaymentMethods = (options) => {
  return fetch("post", `${API_HOST}landing/ptp/paymethods`, {}, options);
};
const addPaymentMethod = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/addpaymethods`, data, options);
};
const removePaymentMethod = (data, options) => {
  return fetch(
    "post",
    `${API_HOST}landing/ptp/deletepaymethods`,
    data,
    options
  );
};
const getBalance = (options) => {
  return fetch("get", `${API_HOST}wallet/balances`, {}, options);
};
const getConvertedPrice = (data, options) => {
  return fetch("post", `${API_HOST}landing/ptp/convertAmount`, data, options);
};
export const CoinService = {
  getCryptoCoins,
  getFiatCoins,
  getPaymentMethods,
  addPaymentMethod,
  removePaymentMethod,
  getBalance,
  getConvertedPrice,
};
