import { OfferService } from "../../services/OfferService";
import { TOKEN } from "../../config/Constants";
import { toast } from "../../Components/Toast/Toast";

import { startStopLoading } from "./loading";
const GET_OFFER_BUY_SELL = "GET_OFFER_BUY_SELL";
const GET_OFFER_BUY_SELL_SELF = "GET_OFFER_BUY_SELL_SELF";
const GET_OFFER_BUY_SELL_BY_ID = "GET_OFFER_BUY_SELL_BY_ID";
const GET_BIDS_BUY_SELL = "GET_BIDS_BUY_SELL";
const GET_BIDS_BUY_SELL_RECIEVED = "GET_BIDS_BUY_SELL_RECIEVED";
const SHOW_HIDE_USER_DETAIL = "SHOW_HIDE_USER_DETAIL";
const SAVE_USER_DETAIL = "SAVE_USER_DETAIL";

const initialState = {
  jwtToken: undefined,
  buySellOffer: [],
  totalOffer: "",
  buySellOfferSelf: [],
  totalOfferSelf: "",
  offerDetailById: [],
  myBids: [],
  totalBids: "",
  myRecievedBids: [],
  totalBidsrecieved: "",
  showUserDetail: false,
  bid_user: {}
};

export default function offer(state = initialState, action = {}) {
  switch (action.type) {
    case GET_OFFER_BUY_SELL:
      return {
        ...state,
        buySellOffer: action.result,
        totalOffer: action.total
      };
    case GET_OFFER_BUY_SELL_SELF:
      return {
        ...state,
        buySellOfferSelf: action.result,
        totalOfferSelf: action.total
      };
    case GET_OFFER_BUY_SELL_BY_ID:
      return {
        ...state,
        offerDetailById: action.result
      };
    case GET_BIDS_BUY_SELL:
      return {
        ...state,
        myBids: action.result,
        totalBids: action.total
      };
    case GET_BIDS_BUY_SELL_RECIEVED:
      return {
        ...state,
        myRecievedBids: action.result,
        totalBidsrecieved: action.total
      };

    case SHOW_HIDE_USER_DETAIL:
      return {
        ...state,
        showUserDetail: action.result
      };
    case SAVE_USER_DETAIL:
      return {
        ...state,
        bid_user: action.result
      };
    default:
      return state;
  }
}
export const getBuySellOffer = data => {
  return (dispatch, getState) => {
    // jwt: state["persist"]["c_temp_user"]["token"]
    dispatch(startStopLoading(true));
    return new Promise((resolve, reject) => {
      OfferService.getOfferBuySell(data, {
        jwt: localStorage.getItem("JwtToken")
      })
        .then(res => {
          dispatch(startStopLoading(false));

          if (data.self === 1) {
            dispatch({
              type: GET_OFFER_BUY_SELL_SELF,
              result: res.data.data,
              total: res.data.total
            });
          } else {
            dispatch({
              type: GET_OFFER_BUY_SELL,
              result: res.data.data,
              total: res.data.total
            });
          }
          resolve(res);
        })
        .catch(ex => {
          dispatch(startStopLoading(false));

          reject(ex);
        });
    });
  };
};
